import { CateringItem } from "./catering-item.class";
import { HashMap } from "../../../shared/utilities/types.utilities";

export class CateringCategory extends CateringItem {
    /** List of items associated with the category */
    public readonly items: readonly CateringItem[];
    /** Whether child items are part of a package */
    public readonly package: boolean;
    /** List of zone ids associated with the category */
    public readonly zones: readonly string[];

    public get can_order_anytime(): boolean {
        return (
            this.order_anytime ||
            this.items.reduce(
                (anytime, item) => anytime || item.can_order_anytime,
                false
            )
        );
    }

    constructor(data: HashMap) {
        super(data);
        this.items = (data.items || []).map(item =>
            item.items ? new CateringCategory(item) : new CateringItem(item)
        );
        this.package = data.package === 'true' || data.package === true;
        this.zones = data.zones && data.zones.length ? [ ...data.zones ] : [];
        (this as any).must_select = this.must_select || this.items.length;
        (this as any).order_anytime = !!data.order_anytime && this.package;
    }

    /**
     * Convert class object into plain object
     */
    public toJSON(this: CateringItem): HashMap {
        const obj = super.toJSON();
        obj.items = obj.items.map((item: CateringItem) => item.toJSON());
        obj.order_anytime = !!obj.order_anytime && obj.package;
        return obj;
    }
}
