import { HashMap } from 'src/app/shared/utilities/types.utilities';
import { randomInt } from 'src/app/shared/utilities/general.utilities';

export function generateMockLocation(overrides: HashMap, fixed_locations: string[], maps: string[]): HashMap {
    const fixed = randomInt(999_999_999) % 2 === 0;
    if (fixed && (!overrides || overrides.fixed)) {
        return {
            map_id: fixed_locations[randomInt(fixed_locations.length)],
            level: maps[randomInt(maps.length)],
            ...overrides
        }
    } else {
        return {
            x: randomInt(900, 100),
            x_max: 1000,
            y: randomInt(500, 100),
            level: maps[randomInt(maps.length)],
            confidence: randomInt(30),
            ...overrides
        }
    }
}
