import { Component, Input, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';

import { Booking } from 'src/app/services/data/bookings/booking.class';
import { humaniseDuration } from 'src/app/shared/utilities/general.utilities';
import { RECURRENCE_PERIODS } from 'src/app/shared/utilities/formatting.utilities';
import { BookingModalComponent } from '../../booking-modal/booking-modal.component';

import * as dayjs from 'dayjs';
import { HashMap } from 'src/app/shared/utilities/types.utilities';

@Component({
    selector: 'meeting-details',
    templateUrl: './details.component.html',
    styleUrls: ['./details.component.scss'],
})
export class MeetingDetailsDisplayComponent implements OnInit {
    /** Booking to display details */
    @Input() public booking: Booking;
    /** Mapping of edited fields */
    public edited: HashMap<boolean> = {};

    /** Display string for the locations in the booking */
    public get location() {
        return this.booking.space_list.map((space) => space.name).join('\n');
    }
    /** Display string for the booking duration */
    public get duration() {
        return this.booking ? humaniseDuration(this.booking.duration) : '1 hour';
    }
    /** Display string for attendees in booking */
    public get start_time() {
        return dayjs(this.booking.date).format('DD MMM YYYY, h:mm A');
    }
    /** Display string for attendees in booking */
    public get end_time() {
        return dayjs(this.booking.date)
            .add(this.booking.duration, 'm')
            .format('DD MMM YYYY, h:mm A');
    }
    /** Display string for attendees in booking */
    public get attendees() {
        return this.booking.attendees.map((person) => person.name).join('\n');
    }

    /** Recurrence setting for booking */
    public get recurrence(): string {
        const { end, period } = this.booking.recurrence;
        /* istanbul ignore if */
        if (!period) {
            return '<No recurrence>';
        }
        const pattern = RECURRENCE_PERIODS.find((i) => i.toLowerCase() === `${period}`);
        const date = dayjs(end).format('DD MMM YYYY');
        return `${pattern} until ${date}`;
    }

    /** Whether booking has been declined or ended */
    public get expired(): boolean {
        return this.booking.status === 'done' || this.booking.declined;
    }

    constructor(private _dialog: MatDialog) {}

    public ngOnInit(): void {
        Object.values(this.booking.edits || []).forEach(i => {
            this.edited[i] = true;
        });
    }

    public editMeeting() {
        this._dialog.open(BookingModalComponent, {
            data: {
                booking: this.booking,
            },
        });
    }
}
