import { Component, OnInit, Input, SimpleChanges, OnChanges, EventEmitter, Output } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';

import { Booking } from 'src/app/services/data/bookings/booking.class';
import { User } from 'src/app/services/data/users/user.class';
import { BaseDirective } from 'src/app/shared/base.directive';
import { ApplicationService } from 'src/app/services/app.service';

@Component({
    selector: 'a-visitor-form',
    templateUrl: './visitor-form.component.html',
    styleUrls: ['./visitor-form.component.scss'],
})
export class VisitorFormComponent extends BaseDirective implements OnChanges {
    /** Booking that the user is assoicated with */
    @Input() public event: Booking;
    /** User details to display */
    @Input() public person: User;
    /** Form control for attendees */
    @Input() public field: FormControl;
    /** Emitter for changes to the person */
    @Output() public personChange = new EventEmitter<User>();
    /** Form fields for visitor details */
    public form: FormGroup;

    constructor(private _service: ApplicationService) {
        super();
    }

    public ngOnChanges(changes: SimpleChanges) {
        /* istanbul ignore else */
        if (changes.person) {
            delete this.form;
            this.form = new FormGroup({
                name: new FormControl(this.person.name, [Validators.minLength(2)]),
                email: new FormControl(this.person.email, [Validators.required, Validators.email]),
            });
        }
    }

    public updatePerson() {
        this.form.markAllAsTouched();
        /* istanbul ignore else */
        if (this.form.valid) {
            this.person = new User({ ...this.form.value, visitor: true });
            this.personChange.emit(this.person);
        }
    }
}
