import { Injectable } from '@angular/core';
import { ComposerService } from '@placeos/composer';
import { first } from 'rxjs/operators';

import { BaseAPIService } from '../base.service';
import { Space, ISpaceAvailabilityOptions } from './space.class';
import { HashMap, ApplicationLoadingState } from '../../../shared/utilities/types.utilities';
import { availabilityOptionsToQuery } from './space.utilities';
import { ServiceManager } from '../service-manager.class';
import { OrganisationService } from '../organisation/organisation.service';
import { ApplicationService } from '../../app.service';

import * as dayjs from 'dayjs';

@Injectable({
    providedIn: 'root'
})
export class SpacesService extends BaseAPIService<Space> {
    constructor(protected _composer: ComposerService, private _org: OrganisationService, private _service: ApplicationService) {
        super(_composer);
        ServiceManager.setService(Space, this);
        this._name = 'Space';
        this._api_route = '/rooms';
        this._compare = (a, b) => !a.id.localeCompare(b.id) || !a.email.localeCompare(b.email);
        this._list_filter = (a: Space) => {
            const bld = this._org.building;
            return a.level.building_id === bld.id;
        };
        this._org.initialised.pipe(first((_) => _)).subscribe(() => this.init());
    }

    /**
     * Get available spaces
     * @param options
     */
    public available(options: ISpaceAvailabilityOptions): Promise<Space[]> {
        if (!options) {
            throw new Error('Space avilability requires request options');
        }
        if (!options.date) {
            options.date = dayjs()
                .startOf('m')
                .valueOf();
        }
        const key = `available|${options.id ? options.id : ''}`;
        if (!this._promises[key]) {
            this._promises[key] = new Promise((resolve, reject) => {
                const respond = (list: Space[]) => {
                    const available_spaces = list.filter(i => {
                        const linked = list.filter(j => i.linked_spaces.indexOf(j.id) >= 0);
                        return (
                            i.was_available && linked.reduce((a, v) => a && v.was_available, true)
                        );
                    });
                    delete this._promises[key];
                    resolve(available_spaces);
                };
                const error = e => {
                    reject(e);
                    delete this._promises[key];
                };
                const query = availabilityOptionsToQuery(options);
                if (options.id) {
                    this.show(options.id, query).then(i => respond([i]), error);
                } else {
                    this.query(query).then(respond, error);
                }
            });
        }
        return this._promises[key];
    }

    /**
     * Load initial data for the service
     */
    protected async load(): Promise<void> {
        const loading: ApplicationLoadingState = this._service.get('loading') || {};
        if (!loading.spaces) {
            loading.spaces = { message: 'Loading space data', state: 'loading' };
            this._service.set('loading', loading);
        }
        await this.query().catch(() => {
            loading.spaces = { message: 'Loading space data', state: 'failed' };
            this._service.set('loading', loading);
        });
        loading.spaces = { message: 'Loading space data', state: 'complete' };
        this._service.set('loading', loading);
    }

    /**
     * Convert raw data into API object
     * @param raw_data Raw API data
     */
    protected process(raw_data: HashMap): Space {
        return new Space(raw_data);
    }
}
