import { Component, OnInit, Input, SimpleChanges } from '@angular/core';
import { Subject, Observable, of } from 'rxjs';
import { debounceTime, distinctUntilChanged, switchMap, catchError } from 'rxjs/operators';

import { BaseDirective } from 'src/app/shared/base.directive';
import { Space } from 'src/app/services/data/spaces/space.class';
import { ApplicationService } from 'src/app/services/app.service';

import * as dayjs from 'dayjs';
import { SpacesService } from 'src/app/services/data/spaces/spaces.service';
import { OrganisationService } from 'src/app/services/data/organisation/organisation.service';

@Component({
    selector: 'week-view-timeline',
    templateUrl: './timeline.component.html',
    styleUrls: ['./timeline.component.scss'],
})
export class WeekViewTimelineComponent extends BaseDirective implements OnInit {
    /** Timestamp of the active date */
    @Input() public date: number;
    /** ID of the active level */
    @Input() public level: string;
    /** ID of the room type to filter displayed spaces by */
    @Input() public space_type: string;
    /** Whether weekends are displayed */
    @Input() public weekends: boolean;
    /** Whether space bookings are being loaded */
    @Input() public loading: boolean;
    /** List of dates to display */
    public date_list: number[] = [];
    /** List of spaces to display bookings for */
    public space_list: Space[] = [];
    /** List of items from an API search */
    public search_results$: Observable<Space[]>;
    /** Subject holding the value of the search */
    public readonly search$ = new Subject<string>();

    constructor(private _spaces: SpacesService, private _org: OrganisationService) {
        super();
    }

    public ngOnInit(): void {
        this.date_list = this.generateDates(this.date, this.weekends);
        this.initBookings();
        this.search$.next(`${this.date}|${this.weekends}|${this.level}`);
        this.interval(
            'update_booking',
            () =>
                this.search$.next(`${this.date}|${this.weekends}|${this.level}|${dayjs().unix()}`),
            30 * 1000
        );
    }

    public ngOnChanges(changes: SimpleChanges): void {
        /* istanbul ignore else */
        if (changes.date || changes.weekends || changes.level) {
            this.date_list = this.generateDates(this.date, this.weekends);
            this.search$.next(`${this.date}|${this.weekends}|${this.level}`);
        }
    }

    /**
     * Generate list of dates to display
     * @param date Currently selected date
     * @param weekends Whether weekends should be included
     */
    private generateDates(date: number, weekends: boolean = false): number[] {
        const list: number[] = [];
        let day = dayjs(date).startOf('d');
        let start = day.startOf('w');
        const end = start.endOf('w');
        while (start.isBefore(end, 'm')) {
            if ((start.day() !== 0 && start.day() !== 6) || weekends) {
                list.push(start.valueOf());
            }
            start = start.add(1, 'd');
        }
        return list;
    }

    private initBookings() {
        // Listen for input changes
        this.search_results$ = this.search$.pipe(
            debounceTime(100),
            distinctUntilChanged(),
            switchMap((_) => {
                this.loading = true;
                const date = dayjs(this.date).startOf('w');
                return this._spaces.query({
                    zone_ids: `${this.level ? this.level : this._org.building.id}${
                        this.space_type ? ',' + this.space_type : ''
                    }`,
                    available_from: Math.floor(this.date_list[0] / 1000) || date.unix(),
                    available_to:
                        Math.floor(this.date_list[this.date_list.length - 1] / 1000) ||
                        date.endOf('w').unix(),
                });
            }),
            catchError((_) => of([]))
        );
        // Process API results
        this.subscription(
            'search_results',
            this.search_results$.subscribe((list) => {
                this.loading = false;
                this.space_list = list;
            })
        );
    }
}
