import {
    Component,
    OnChanges,
    Input,
    SimpleChanges,
    Output,
    EventEmitter,
    ViewChild,
    ElementRef,
    OnInit,
} from '@angular/core';
import { CdkDragMove, CdkDragDrop } from '@angular/cdk/drag-drop';

import { IEventOverlap } from './event/event.component';

import * as dayjs from 'dayjs';
import { BaseDirective } from 'src/app/shared/base.directive';
import { Space } from 'src/app/services/data/spaces/space.class';
import { ApplicationService } from 'src/app/services/app.service';
import { Booking } from 'src/app/services/data/bookings/booking.class';
import { first } from 'rxjs/operators';
import { ConfirmModalComponent } from 'src/app/overlays/confirm-modal/confirm-modal.component';
import { MatDialog } from '@angular/material/dialog';
import { DialogEvent, HashMap } from 'src/app/shared/utilities/types.utilities';
import { DayViewSettings } from 'src/app/shared/utilities/settings.interfaces';
import { SpacesService } from 'src/app/services/data/spaces/spaces.service';

@Component({
    selector: 'day-view-space',
    templateUrl: './space.component.html',
    styleUrls: ['./space.component.scss'],
})
export class DayViewSpaceComponent extends BaseDirective implements OnInit, OnChanges {
    /** List of space dropzones connected to */
    @Input() public space_list: string[];
    /** Date to display events for */
    @Input() public date: number;
    /** Space to display events for */
    @Input() public space: Space;
    /** Scroll value of the day view */
    @Input() public scroll: number;
    /** Only show events with setup and/or breakdown times */
    @Input() public overflow_only: boolean;
    /** Mapping of whether to show events with a given legend id */
    @Input() public legend: HashMap<boolean> = {};
    /** Scroll value to add to drop placeholders */
    @Input('scrollTop') public scroll_top: number;
    /** Last update time of the displayed bookings */
    @Input('lastUpdate') public last_update: number;
    /** Emitter for whether the space column is in view */
    @Output() public inView = new EventEmitter<boolean>();
    /** Emitter for whether an event is being dragged */
    @Output() public dragging = new EventEmitter<boolean>();
    /** Mapping of event IDs to overlap details */
    public overlap_details: HashMap<IEventOverlap> = {};
    /** Mapping of positions of event in the day */
    public position_list: HashMap<{ top: number; height: number }> = {};
    /** Override for mapped position */
    public top: number;
    /** Client Rectangle for the container element box */
    public box: ClientRect;
    /** Whether the space list is in view */
    public in_view: boolean;
    /** ID of the booking with an action processing */
    public override_state: string;
    /** Map of bookings to whether they are loading */
    public loading: HashMap<boolean> = {};
    /** Settings for day view */
    public settings: DayViewSettings = {} as any;

    /** Container element for the component contents */
    @ViewChild('view', { static: true }) private view: ElementRef<HTMLDivElement>;

    constructor(
        private _service: ApplicationService,
        private _spaces: SpacesService,
        private _dialog: MatDialog
    ) {
        super();
    }

    /** Events to display for the active date */
    public get events(): Booking[] {
        /* istanbul ignore else */
        if (this.space) {
            const bookings = this.space.bookingsFor(this.date);
            const list = bookings.filter((event) => {
                if (this.override_state && this.override_state === event.icaluid) {
                    this.loading[event.id] = true;
                }
                return event.declined
                    ? (!this.overflow_only ||
                          event.setup[event.space.email] ||
                          event.breakdown[event.space.email]) &&
                          this.legend.declined
                    : (!this.overflow_only ||
                          event.setup[event.space.email] ||
                          event.breakdown[event.space.email]) &&
                          this.legend[event.type];
            });
            return list;
        }
        return [];
    }

    public ngOnInit(): void {
        this._service.initialised.pipe(first((_) => _)).subscribe(() => {
            this.settings = this._service.setting('app.day_view') || {};
            this.subscription(
                'breakdown',
                this._service
                    .listen('APP.breakdown')
                    .subscribe((state) => (this.overflow_only = state))
            );
            this.subscription(
                'legend',
                this._service
                    .listen('CONCIERGE.legend')
                    .subscribe((state) => (this.legend = state || {}))
            );
        });
    }

    public ngOnChanges(changes: SimpleChanges): void {
        /* istanbul ignore else */
        if (changes.scroll) {
            this.checkInView();
        }
        /* istanbul ignore else */
        if (changes.last_update) {
            this.timeout('update_overlaps', () => this.calculateOverlaps(), 1000);
        }
    }

    /**
     * Track by function for ngFor optimisation
     * @param index Index of the item
     * @param item Data associated with the item at the given index
     */
    public trackByFn(index: number, item: Booking) {
        return item.id || index;
    }

    /**
     * Calculate to overlap details for the booking events on the space
     */
    private calculateOverlaps() {
        const events = this.events.sort(
            (a, b) =>
                a.date - b.date ||
                a.duration - b.duration ||
                (a.title || '').localeCompare(b.title || '')
        );
        events.forEach((i) => (this.overlap_details[i.id] = { index: 0, total: 1 }));
        for (const bkn of events) {
            const bkn_start = dayjs(bkn.date).startOf('m');
            const bkn_end = dayjs(bkn_start).add(bkn.duration, 'm').startOf('m');
            let count = 1;
            let index = 0;
            const collisions: Booking[] = [];
            for (const cmp of events) {
                /* istanbul ignore else */
                if (bkn.id !== cmp.id) {
                    const cmp_start = dayjs(cmp.date).startOf('m');
                    const cmp_end = dayjs(cmp_start).add(cmp.duration, 'm').startOf('m');
                    /* istanbul ignore else */
                    if (
                        bkn_start.isSame(cmp_start, 'm') ||
                        (bkn_start.isAfter(cmp_start, 'm') && bkn_start.isBefore(cmp_end, 'm')) ||
                        bkn_end.isSame(cmp_end, 'm') ||
                        (bkn_end.isAfter(cmp_start, 'm') && bkn_end.isBefore(cmp_end, 'm'))
                    ) {
                        count++;
                        collisions.push(bkn);
                        /* istanbul ignore else */
                        if (
                            events.findIndex((i) => i.id === bkn.id) >
                            events.findIndex((i) => i.id === cmp.id)
                        ) {
                            index++;
                        }
                    }
                }
            }
            this.overlap_details[bkn.id] = { index: Math.max(0, index), total: count };
        }
    }

    /**
     * Check whether the space column is with the view of the
     */
    private checkInView() {
        /* istanbul ignore else */
        if (this.view && this.view.nativeElement) {
            const el = this.view.nativeElement;
            this.in_view =
                el.offsetLeft + this.scroll >= -240 &&
                el.offsetLeft + this.scroll <= window.innerWidth;
            this.inView.emit(this.in_view);
        }
    }

    /**
     * Initialise drag events
     */
    public initDrag() {
        this.box = this.view.nativeElement.getBoundingClientRect();
        this.dragging.emit(true);
    }

    /**
     * Cleanup after drag
     */
    public endDrag() {
        this.timeout('end_drag', () => (this.top = 0), 10);
        this.dragging.emit(false);
    }

    /**
     * Update top position of the dragged event placeholder
     * @param booking Booking being dragged
     * @param event Drag event
     */
    public updateDragTop(event: CdkDragMove<Booking>) {
        /* istanbul ignore else */
        if (event.delta.y) {
            const y = event.pointerPosition.y;
            const top = (y - this.box.top - this.scroll_top) / this.box.height;
            const top_minutes = (Math.round((top * 24 * 60) / 15) * 15) / 60;
            this.top = (top_minutes / 24) * 100;
        }
    }

    /**
     * Handle drop event
     * @param event
     */
    public drop(event: CdkDragDrop<{ top: number; list: Booking[] }>): void {
        const id = event.item.element.nativeElement.id;
        const booking = event.previousContainer.data.list.find((i) => i.id === id);
        /* istanbul ignore else */
        if (!booking) {
            return;
        }
        const top = event.previousContainer.data.top;
        const new_start = Math.round(((top || 0) / 100) * 24 * 60);
        const old_date = dayjs(booking.date);
        const date = old_date.startOf('d').minute(new_start);
        const now = dayjs().startOf('m');
        /* istanbul ignore else */
        // Prevent moving event into past
        if (now.isAfter(date, 'm')) {
            this.handleDropError(booking.id, 'Meetings cannot be moved into the past');
            return;
        }
        // Check for collisions
        this.loading[booking.id] = true;
        this.override_state = booking.icaluid;
        let room = booking.space;
        let message = `Update ${booking.organiser.name}'s meeting in "${
            booking.space.name
        }" from ${old_date.format('h:mm A')} to ${date.format('h:mm A')}`;
        let success = `Successfully updated meeting time from ${old_date.format(
            'h:mm A'
        )} to ${date.format('h:mm A')}`;
        /* istanbul ignore else */
        if (event.container !== event.previousContainer) {
            // Same room
            room = this._spaces.find(event.container.id.replace('space-col-', '')) || room;
            message = `Move ${booking.organiser.name}'s meeting in "${booking.space.name}" to "${
                room.name
            }" at ${date.format('h:mm A')}`;
            success = `Successfully moved booking to "${room.name}" at ${date.format('h:mm A')}`;
        }
        /* istanbul ignore else */
        // Check if booking has a change
        if (room === booking.space && date.isSame(old_date, 'm')) {
            this.handleDropError(booking.id, 'The meeting is already in this room');
            return;
        }
        const room_list = [room, ...booking.space_list.filter((i) => i.id !== booking.space.id)];
        // Check if booking is already in the room
        const room_count = room_list.reduce((a, v) => a.add(v.id), new Set()).size;
        /* istanbul ignore else */
        if (room_count < room_list.length) {
            this.handleDropError(booking.id, 'The meeting is already in this room');
            return;
        }
        const new_booking = new Booking({
            ...booking,
            room_ids: room_list.map((space) => space.id),
        });
        this.checkCollisions(
            room_list,
            date.valueOf(),
            new_booking.duration,
            new_booking.icaluid
        ).then(
            () => {
                this.moveBooking(message, new_booking).then(
                    (_) => {
                        this.override_state = null;
                        this._service.notifySuccess(success);
                    },
                    (err) => this.handleDropError(new_booking.id, `Error: ${err}`)
                );
            },
            () => this.handleDropError(new_booking.id, 'Updated meeting time clashes with another')
        );
    }

    /**
     * Check if there are any booking collisions with the new booking time
     * @param spaces
     * @param date
     * @param duration
     */
    private checkCollisions(
        spaces: Space[],
        date: number,
        duration: number,
        ignore?: string
    ): Promise<void> {
        return new Promise((resolve, reject) => {
            this._spaces
                .available({
                    room_ids: spaces.map((i) => i.id).join(','),
                    date,
                    duration,
                    ignore,
                    hide_bookings: true,
                })
                .then(
                    (list) => {
                        list.length === spaces.length ? resolve() : reject();
                    },
                    () => reject()
                );
        });
    }

    /**
     * Handle errors from drop move logic
     * @param id Booking ID
     * @param message Error description
     */
    private handleDropError(id: string, message: string) {
        this.loading[id] = false;
        this.override_state = null;
        this._service.notifyError(message);
    }

    /**
     * Update booking
     * @param confirm_msg
     * @param new_booking
     */
    private moveBooking(confirm_msg: string, new_booking: Booking): Promise<Booking> {
        return new Promise((resolve, reject) => {
            const ref = this._dialog.open<ConfirmModalComponent>(ConfirmModalComponent, {
                data: {
                    title: 'Update meeting',
                    content: confirm_msg,
                    icon: { type: 'icon', class: 'material-icons', content: 'event_available' },
                    action: 'Ok',
                },
            });
            this.subscription(
                'confirm',
                ref.componentInstance.event.subscribe((event: DialogEvent) => {
                    /* istanbul ignore else */
                    if (event.reason === 'done') {
                        new_booking.save().then(
                            (bkn: Booking) => resolve(bkn),
                            (err) => reject(err)
                        );
                    }
                    ref.close();
                })
            );
        });
    }
}
