import { Injectable } from '@angular/core';

import { ComposerService } from '@placeos/composer';

import { BaseAPIService } from '../base.service';
import { Organisation } from './organisation.class';
import { Building } from './building.class';
import {
    HashMap,
    ApplicationLoadingState,
    Identity,
} from '../../../shared/utilities/types.utilities';
import { BuildingLevel } from './level.class';
import { User } from '../users/user.class';
import { first } from 'rxjs/operators';
import { ApplicationService } from '../../app.service';
import { ServiceManager } from '../service-manager.class';
import { UsersService } from '../users/users.service';

@Injectable({
    providedIn: 'root',
})
export class OrganisationService extends BaseAPIService {
    /** Organisation data for the application */
    private _organisation: Organisation;
    /** Actively displayed building */
    private _active_building: string;

    constructor(
        protected _composer: ComposerService,
        private _service: ApplicationService,
        private _users: UsersService
    ) {
        super(_composer);
        ServiceManager.setService(Organisation, this);
        ServiceManager.setService(Building, this);
        this._name = 'Organisation';
        this._api_route = '/zones';
        this.set('buildings', []);
        this.set('active_building', null);
        this._users.initialised.pipe(first((_) => _)).subscribe(() => this.init());
    }

    /**
     * Add is not available on organisation service
     */
    public async add(form_data: HashMap, query_params?: HashMap): Promise<Organisation> {
        throw new Error('Add is not available on the organisation service');
    }

    /**
     * Update is not available on organisation service
     */
    public async update(
        id: string,
        form_data: HashMap,
        query_params?: HashMap
    ): Promise<Organisation> {
        throw new Error('Update is not available on the organisation service');
    }

    /**
     * Delete is not available on organisation service
     */
    public async delete(id: string): Promise<void> {
        throw new Error('Delete is not available on the organisation service');
    }

    /**
     * Get list of levels for the given building ID
     * @param bld_id Building ID
     */
    public levels(bld_id: string): any[] {
        return (this.buildings.find((i) => i.id === bld_id) || ({} as Building)).levels;
    }

    /**
     * Get a setting from the organisation or active building
     * @param key Name of the setting. i.e. nested items can be grabbed using `.` to seperate key names
     */
    public setting(key: string) {
        return this.building.setting(key) || this._organisation.setting(key);
    }

    /** Active building */
    public get building(): Building {
        return this.buildings.find((i) => i.id === this._active_building);
    }

    /** List of types of spaces */
    public get space_types(): Identity[] {
        return this.get('space_types') || [];
    }

    public set building(bld: Building) {
        if (bld instanceof Building) {
            this._active_building = bld.id;
        } else {
            this._active_building = bld;
        }
        this.set('active_building', this.building);
    }

    /** List of buildings for the organisation */
    public get buildings(): Building[] {
        return this.get<Building[]>('buildings') || [];
    }

    /**
     * Get list of available equipment
     * @param id ID of the building to get the list from. i.e. Defaults to the active building
     */
    public getExtras(id: string, bld_id?: string) {
        const bld = this.buildings.find((i) => i.id === bld_id) || this.building;
        if (bld && id) {
            return bld.extras.filter((i) => `${i.id}`.indexOf(id) >= 0);
        }
        return [];
    }

    /**
     * Get the first level matching the list of given IDs
     * @param ids List of ID to search with
     */
    public levelWithID(ids: string | string[]): BuildingLevel {
        const list = ids instanceof Array ? ids : [ids];
        const bld_list = this.buildings;
        for (const id of list) {
            for (const bld of bld_list) {
                for (const lvl of bld.levels) {
                    if (lvl.id === id) {
                        return lvl;
                    }
                }
            }
        }
        return null;
    }

    /**
     * Initialise service data
     */
    protected async load(): Promise<void> {
        /* istanbul ignore else */
        if (localStorage) {
            this._active_building = localStorage.getItem(`CONCIERGE.building`);
        }
        const loading: ApplicationLoadingState = this._service.get('loading') || {};
        loading.organisation = { message: 'Loading organisation data', state: 'loading' };
        await this.loadOrganisation();
        loading.organisation = { message: 'Loading organisation data', state: 'complete' };
        loading.buildings = { message: 'Loading building data', state: 'loading' };
        this._service.set('loading', loading);
        await this.loadBuildings();
        loading.buildings = { message: 'Loading building data', state: 'complete' };
        loading.levels = { message: 'Loading building floor data', state: 'loading' };
        this._service.set('loading', loading);
        await this.loadLevels();
        loading.levels = { message: 'Loading building floor data', state: 'complete' };
        this._service.set('loading', loading);
        loading.space_types = { message: 'Loading space type data', state: 'loading' };
        this._service.set('loading', loading);
        await this.loadSpaceTypes();
        loading.space_types = { message: 'Loading space type data', state: 'complete' };
        this._service.set('loading', loading);
        const user = this._users.current;
        if (user) {
            const building = this.buildings.find((bld) => bld.code === user.location);
            if (building) {
                this._active_building = building.id;
                this.set('active_building', building);
            } else {
                this._active_building = this.building.id;
                this.set('active_building', this.building);
            }
        }
    }

    /**
     * Load organisation data for application
     */
    public async loadOrganisation(): Promise<void> {
        const org_data = await this.query({ tags: 'org', engine: true });
        this._organisation = new Organisation(org_data[0]);
        this.set('organisation', this._organisation);
    }

    /**
     * Load building data for the organisation
     */
    public async loadBuildings(): Promise<void> {
        const bld_data = await this.query({ tags: 'building', engine: true, limit: 500 });
        const buildings = (bld_data as HashMap[]).map((i) => new Building(i));
        this.set('buildings', buildings);
        /* istanbul ignore else */
        if (!this._active_building && buildings && buildings.length > 0) {
            this._active_building = buildings[0].id;
        }
    }

    /**
     * Load level data for the buildings
     */
    public async loadLevels(): Promise<void> {
        const lvl_data = await this.query({ tags: 'level', engine: true });
        const levels = (lvl_data as HashMap[]).map((i) => new BuildingLevel(i));
        this.set('levels', levels);
    }

    /**
     * Load space type data for the buildings
     */
    public async loadSpaceTypes(): Promise<void> {
        const type_data = await this.query({ tags: 'room', engine: true });
        const types = (type_data as HashMap[]).map((i) => ({ id: i.id, name: i.name }));
        this.set('space_types', types);
    }
}
