import { Component, Input, Output, EventEmitter } from '@angular/core';
import { trigger, style, animate, transition } from '@angular/animations';
import { MatDialog } from '@angular/material/dialog';

import { BaseDirective } from 'src/app/shared/base.directive';
import { Booking } from 'src/app/services/data/bookings/booking.class';
import { ApplicationService } from 'src/app/services/app.service';
import { CateringOrder } from 'src/app/services/data/catering/catering-order.class';
import { DialogEvent } from 'src/app/shared/utilities/types.utilities';
import { ConfirmModalComponent } from 'src/app/overlays/confirm-modal/confirm-modal.component';
import { MeetingDetailsModalComponent } from 'src/app/overlays/meeting-details-modal/meeting-details-modal.component';
import { BookingsService } from 'src/app/services/data/bookings/bookings.service';

const STATES: { [id: string]: string } = {};

@Component({
    selector: 'approvals-event',
    templateUrl: './event.component.html',
    styleUrls: ['./event.component.scss'],
    animations: [
        trigger('show', [
            transition(':enter', [
                style({ opacity: 0, height: 0 }),
                animate(200, style({ opacity: 1, height: '*' })),
            ]),
            transition(':leave', [
                style({ opacity: 1, height: '*' }),
                animate(200, style({ opacity: 0, height: 0 })),
            ]),
        ]),
    ],
})
export class DayViewApprovalsEventComponent extends BaseDirective {
    /** Event to display */
    @Input() event: Booking;
    /** Event emitter for event actions */
    @Output() acting = new EventEmitter<boolean>();
    /** Whether approval action is being processed */
    public loading: boolean;
    /** Whether event details are shown */
    public shown: boolean;

    public get was_edited(): boolean {
        return this.event.edits.length > 0;
    }

    /** Type of event */
    public get type(): string {
        return this.event.type;
    }

    /** Person to notify when external attendees arrive */
    public get notifyee() {
        return 'Concierge';
    }

    /** Description of the meeting */
    public get description(): string {
        return this.event.description;
    }

    /** Whether the meeting has catering attached */
    public get has_catering(): boolean {
        const catering: readonly CateringOrder[] = this.event.catering;
        return !!catering.find((order) => this.event.space.id === order.location_id);
    }

    /** View booking details */
    public view() {
        this.timeout(
            'view',
            () => {
                this._dialog.open(MeetingDetailsModalComponent, {
                    data: {
                        booking: this.event,
                    },
                });
                this._service.set('CONCIERGE.day_view.viewing', this.event);
            },
            50
        );
    }

    constructor(
        private _service: ApplicationService,
        private _bookings: BookingsService,
        private _dialog: MatDialog
    ) {
        super();
    }

    /**
     * Approve the meeting for the set room
     */
    public accept() {
        this.acting.emit(true);
        const ref = this._dialog.open(ConfirmModalComponent, {
            data: {
                title: 'Approve Meeting',
                content: `Approve ${this.event.organiser.name}'s meeting in "${this.event.space.name}" at ${this.event.start_time} for ${this.event.length_string} on ${this.event.date_string}`,
                icon: { type: 'icon', class: 'material-icons', content: 'event_available' },
                action: 'Ok',
            },
        });
        ref.componentInstance.event.subscribe((event: DialogEvent) => {
            /* istanbul ignore else */
            if (event.reason === 'done') {
                this.loading = true;
                const event = this.event.toJSON();
                this._bookings
                    .accept(this.event.id, {
                        concierge: true,
                        host: this.event.organiser.email,
                        room_id: this.event.space?.id,
                        icaluid: this.event.icaluid,
                        start: event.start,
                        end: event.end
                    })
                    .then(
                        () => {
                            this._service.notifySuccess('Meeting approved.');
                            this.event.approval_status[this.event.space.id] = 'accepted';
                            STATES[this.event.id] = 'accepted';
                            this._service.set('BOOKING.states', STATES);
                            this.loading = false;
                            this.acting.emit(false);
                        },
                        () => {
                            this.acting.emit(false);
                            this._service.notifyError('Error approving meeting.');
                            this.loading = false;
                        }
                    );
            }
            ref.close();
        });
    }

    /**
     * Decline meeting for the set room
     */
    public decline() {
        this.acting.emit(true);
        const ref = this._dialog.open(ConfirmModalComponent, {
            data: {
                title: 'Decline Meeting',
                content: `Decline ${
                    this.event.organiser.name || this.event.organiser
                }'s meeting in "${this.event.space.name}" at ${this.event.start_time} for ${
                    this.event.length_string
                } on ${this.event.date_string}`,
                icon: { type: 'icon', class: 'material-icons', content: 'event_busy' },
                action: 'Ok',
            },
        });
        ref.componentInstance.event.subscribe((event: DialogEvent) => {
            /* istanbul ignore else */
            if (event.reason === 'done') {
                this.loading = true;
                const booking: Booking = this.event;
                this.loading = true;
                this._bookings
                    .decline(booking.id, {
                        booking_id: booking.id,
                        organiser: booking.organiser.email,
                        icaluid: booking.icaluid,
                        start: Math.floor(booking.date / 1000),
                        end: Math.floor(booking.date / 1000) + booking.duration * 60,
                        room_id: booking.space.id,
                    })
                    .then(
                        () => {
                            this._service.notifySuccess('Meeting declined.');
                            this.event.approval_status[booking.space.id] = 'declined';
                            STATES[this.event.id] = 'declined';
                            this._service.set('BOOKING.states', STATES);
                            this.loading = false;
                            this.acting.emit(false);
                        },
                        () => {
                            this.acting.emit(false);
                            this._service.notifyError('Error declining meeting.');
                            this.loading = false;
                        }
                    );
            }
            ref.close();
        });
    }
}
