import { MockHttpRequestHandler } from '@placeos/ts-client';

import { generateMockBooking } from 'src/app/services/data/bookings/booking.utilities';
import { randomInt, unique } from '../../utilities/general.utilities';
import { MOCK_SPACES } from './spaces.mock';
import { MOCK_USERS, MOCK_CONTACTS } from './users.mock';
import { API } from './common.mock';

import * as dayjs from 'dayjs';

window.control = window.control || {};
window.control.systems = window.control.systems || {};
window.control.handlers = window.control.handlers || [];

export const MOCK_BOOKINGS = Array(1000)
    .fill(0)
    .map((i) => {
        const rooms = unique(
            Array(randomInt(3, 1))
                .fill(0)
                .map((i) => MOCK_SPACES[Math.floor(Math.random() * MOCK_SPACES.length)]),
            'id'
        );
        // Set the organiser with the generated user list
        const organiser =
            randomInt(9999) % 10 === 0
                ? MOCK_USERS[MOCK_USERS.length - 1]
                : MOCK_USERS[randomInt(MOCK_USERS.length)];
        // Set the attendees with the generated user list
        const attendees = unique(
            Array(randomInt(10))
                .fill(0)
                .map((i) => MOCK_USERS[randomInt(MOCK_USERS.length)]),
            'email'
        );
        const guests = unique(
            Array(randomInt(10))
                .fill(0)
                .map((i) => MOCK_CONTACTS[randomInt(MOCK_CONTACTS.length)]),
            'email'
        );
        const booking_data = generateMockBooking({
            organiser,
            attendees: attendees.concat(guests),
            room_ids: rooms.map((i) => i.email),
        });
        for (const space of rooms) {
            booking_data.status[space.email] = ['approved', 'tentative', 'declined'][randomInt(3)];
        }
        return booking_data;
    });

MOCK_SPACES.forEach(
    (i) => (i.bookings = MOCK_BOOKINGS.filter((j) => j.room_ids.indexOf(i.email) >= 0))
);

// Handler for bookings index
window.control.handlers.push({
    path: `${API}/bookings`,
    metadata: MOCK_BOOKINGS,
    method: 'GET',
    callback: (event) => {
        const user = MOCK_USERS[MOCK_USERS.length - 1];
        let data = !event.query_params.email
            ? MOCK_BOOKINGS.filter(
                  (i) =>
                      i.organiser.email === user.email ||
                      i.organiser === user.email ||
                      i.attendees.reduce((a, v) => a || v.email === user.email, false)
              )
            : MOCK_BOOKINGS.filter(
                  (i) =>
                      i.organiser.email === event.query_params.email ||
                      (i.organiser as any) === event.query_params.email ||
                      i.attendees.reduce((a, v) => a || v.email === event.query_params.email, false)
              );
        // Filter bookings between a given period
        if (event.query_params.from) {
            const start = dayjs(+event.query_params.from * 1000);
            const end = event.query_params.to
                ? dayjs(+event.query_params.to * 1000).endOf('m')
                : start.endOf('d');
            data = data.filter((i: any) => {
                const bkn_start = dayjs(i.start_epoch * 1000 || i.start * 1000)
                    .startOf('m')
                    .second(1);
                const bkn_end = dayjs(i.end_epoch * 1000 || i.end * 1000);
                return (
                    (bkn_start.isAfter(start, 's') && bkn_start.isBefore(end, 'm')) ||
                    (bkn_end.isAfter(start, 'm') && bkn_end.isBefore(end, 's'))
                );
            });
        }
        return data;
    },
} as MockHttpRequestHandler);

// Handler for bookings show
window.control.handlers.push({
    path: `${API}/bookings/:id`,
    metadata: MOCK_BOOKINGS,
    method: 'GET',
    callback: (event) => {
        if (event.route_params.id) {
            const booking = MOCK_BOOKINGS.find(
                (i) => i.id === event.route_params.id || i.icaluid === event.route_params.id
            );
            if (booking) {
                return booking;
            } else {
                throw { status: 404, message: 'Booking not found.' };
            }
        } else {
            throw { status: 400, message: 'Invalid booking ID' };
        }
    },
} as MockHttpRequestHandler);

// Handler for new bookings
window.control.handlers.push({
    path: `${API}/bookings`,
    metadata: MOCK_BOOKINGS,
    method: 'POST',
    callback: (event) => {
        if (event.body) {
            event.body.id = `bkn-${randomInt(9_999_999)}`;
            event.body.icaluid = `ical-${event.body.id}`;
            event.body.approval_status = {};
            for (let i = 0; i < event.body.room_ids.length; i++) {
                event.body.approval_status[event.body.room_ids[i]] = event.body.auto_approve[i]
                    ? 'approved'
                    : 'tentative';
            }
            MOCK_BOOKINGS.push(event.body);
            MOCK_SPACES.forEach(
                (space) =>
                    (space.bookings = MOCK_BOOKINGS.filter(
                        (booking) => booking.room_ids.indexOf(space.email) >= 0
                    ))
            );
            return event.body;
        } else {
            throw { status: 500, message: 'Invalid booking data' };
        }
    },
} as MockHttpRequestHandler);

// Handler for updating existing bookings
window.control.handlers.push({
    path: `${API}/bookings/:id`,
    metadata: MOCK_BOOKINGS,
    method: 'PUT',
    callback: (event) => {
        if (event.route_params.id) {
            const index = MOCK_BOOKINGS.findIndex(
                (i) => i.id === event.route_params.id || i.icaluid === event.route_params.id
            );
            if (index >= 0) {
                event.body.approval_status = {};
                for (let i = 0; i < event.body.room_ids.length; i++) {
                    event.body.approval_status[event.body.room_ids[i]] = event.body.auto_approve[i]
                        ? 'approved'
                        : 'tentative';
                }
                MOCK_BOOKINGS[index] = event.body;
                MOCK_SPACES.forEach(
                    (i) =>
                        (i.bookings = MOCK_BOOKINGS.filter((j) => j.room_ids.indexOf(i.email) >= 0))
                );
                return event.body;
            } else {
                throw { status: 404, message: 'Booking not found' };
            }
        } else throw { status: 500, message: 'Invalid booking ID' };
    },
} as MockHttpRequestHandler);

// Handler for deleting exisiting bookings
window.control.handlers.push({
    path: `${API}/bookings/:id`,
    metadata: MOCK_BOOKINGS,
    method: 'DELETE',
    callback: (event) => {
        if (event.route_params.id) {
            const index = MOCK_BOOKINGS.findIndex(
                (i) => i.id === event.route_params.id || i.icaluid === event.route_params.id
            );
            if (index >= 0) {
                MOCK_BOOKINGS.splice(index, 1);
                MOCK_SPACES.forEach(
                    (i) =>
                        (i.bookings = MOCK_BOOKINGS.filter((j) => j.room_ids.indexOf(i.email) >= 0))
                );
            } else {
                throw { status: 404, message: 'Booking not found' };
            }
        } else throw { status: 500, message: 'Invalid booking ID' };
    },
} as MockHttpRequestHandler);
