import {
    Component,
    OnInit,
    Input,
    OnChanges,
    SimpleChanges,
    Output,
    EventEmitter,
} from '@angular/core';
import { Router } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';

import { BaseDirective } from 'src/app/shared/base.directive';
import { Space } from 'src/app/services/data/spaces/space.class';
import { ApplicationService } from 'src/app/services/app.service';
import { CateringOrder } from 'src/app/services/data/catering/catering-order.class';
import { MeetingDetailsModalComponent } from 'src/app/overlays/meeting-details-modal/meeting-details-modal.component';

import * as dayjs from 'dayjs';

/** Details about the associated event overlapping with other events */
export interface IEventOverlap {
    /** Index of this item with the overlapping items */
    index: number;
    /** Total number of items overlapping with this one */
    total: number;
}

@Component({
    selector: 'day-view-space-event',
    templateUrl: './event.component.html',
    styleUrls: ['./event.component.scss'],
})
export class DayViewSpaceEventComponent extends BaseDirective implements OnChanges {
    /** Event to display */
    @Input() public event: any;
    /** Date to display events for */
    @Input() public date: number;
    /** Event to display */
    @Input() public space: Space;
    /** Whether the item is a fixed size */
    @Input() public fixed: boolean;
    /** Overlap details for the event */
    @Input() public overlap: IEventOverlap;
    /** Whether an action is being processed for this event */
    @Input() public loading: boolean;
    /** Emitter for the current position of the event within the timeline */
    @Output() public position = new EventEmitter<{ top: number; height: number }>();

    /** Position of the event start time in the day */
    public top: number;
    /** Position of the event relative to overlapping events */
    public left: number;
    /** Height based of the duration of the event */
    public height: number;
    /** Size of the event when overlapping with other events */
    public width: number;
    /** Offset from the event's top that the setup time starts */
    public overflow_top: number;
    /** Offset from the event's bottom that the breakdown time ends */
    public overflow_bottom: number;
    /** Whether to hide the event */
    public hide: boolean = false;

    constructor(
        private _service: ApplicationService,
        private _dialog: MatDialog,
        private _router: Router
    ) {
        super();
    }

    /**  */
    public get should_display() {
        const date = dayjs(this.date);
        const start = dayjs(this.event.date);
        const end = start.add(this.event.duration, 'm');
        return (
            date.isSame(start, 'd') ||
            date.isSame(end, 'd') ||
            (date.isAfter(start, 'd') && date.isBefore(end, 'd'))
        );
    }

    /** Type of booking */
    public get type(): string {
        if (this.event.declined) {
            return 'cancelled';
        }
        const booking_type = this.event.type;
        if (booking_type === 'internal' && this.event.has_visitors) {
            return 'external';
        }
        return booking_type;
    }

    /** Period that the event will occur during the day */
    public get period(): string {
        return this.event.time_period;
    }

    /** Number attendees expected to turn up to the event */
    public get expected(): number {
        const expected = this.event.expected_attendees;
        return this.space ? expected[this.space.id] || 0 : 0;
    }

    /** Whether the meeting has catering attached */
    public get has_catering(): boolean {
        return !!this.order;
    }

    public get order(): CateringOrder {
        return this.event.catering.find((order) => order.location_id === this.space.id);
    }

    /** View booking details */
    public view() {
        this._dialog.open(MeetingDetailsModalComponent, {
            maxHeight: '95vh',
            maxWidth: '95vw',
            data: {
                booking: this.event,
            },
        });
    }

    /** Whether event is active */
    public get active(): boolean {
        const event = this._service.get('CONCIERGE.day_view.viewing');
        return !event || event.id === this.event.id;
    }

    public ngOnChanges(changes: SimpleChanges) {
        /* istanbul ignore else */
        if (changes.event || changes.overlap || changes.date) {
            this.calculatePosition();
            this.hide = !this.should_display;
        }
    }

    /**
     * View catering order for the event
     */
    public viewOrder(id: string) {
        /* istanbul ignore else */
        if (this.event && this.event.catering) {
            /* istanbul ignore else */
            if (sessionStorage) {
                sessionStorage.setItem('CONCIERGE.view_order', id);
                const booking = JSON.stringify(this.event.toJSON());
                sessionStorage.setItem('CONCIERGE.order_booking', booking);
            }
            this._router.navigate(['/catering', 'orders'], {
                queryParams: { booking: this.event.id, view: id },
            });
        }
    }

    /** Calculate the position */
    private calculatePosition() {
        /* istanbul ignore else */
        if (this.event) {
            const overlap = this.overlap || { index: 0, total: 1 };
            const shown_date = dayjs(this.date).startOf('d');
            const date = dayjs(this.event.date).startOf('m');
            const start = +date.diff(shown_date, 'h', true).toFixed(3);
            const duration = Math.min(
                this.event.duration,
                Math.abs(shown_date.diff(date.add(this.event.duration, 'm'), 'm'))
            );
            this.top = Math.max(0, (start / 24) * 100);
            this.height = Math.min(
                100 - this.top,
                this.fixed ? duration / 60 : (duration / 60 / 24) * 100
            );
            this.position.emit({ top: this.top, height: (duration / 60 / 24) * 100 });
            this.width = Math.min(100, 100 / overlap.total + 5);
            this.left = Math.min(100 - this.width, this.width * overlap.index - 5 * overlap.index);
            this.overflow_top =
                (this.event.setup[this.event.space.email]
                    ? this.event.setup[this.event.space.email] / duration
                    : -0.1) * 100;
            this.overflow_bottom =
                (this.event.breakdown[this.event.space.email]
                    ? this.event.breakdown[this.event.space.email] / duration
                    : -0.1) * 100;
        }
    }
}
