import { Component, Input, Output, EventEmitter } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { FormGroup } from '@angular/forms';

import { Space } from 'src/app/services/data/spaces/space.class';
import { ApplicationService } from 'src/app/services/app.service';
import { DialogEvent } from 'src/app/shared/utilities/types.utilities';
import { BaseDirective } from 'src/app/shared/base.directive';
import {
    BookingRecurrenceDetails,
    BookingRecurrenceSpaceDetails,
} from 'src/app/services/data/bookings/booking.interfaces';
import {
    RecurrenceSpacesModalComponent,
    RecurrenceSpacesModalData,
} from 'src/app/overlays/recurrence-spaces-modal/recurrence-spaces-modal.component';
import { OrganisationService } from 'src/app/services/data/organisation/organisation.service';
import { UsersService } from 'src/app/services/data/users/users.service';

@Component({
    selector: 'a-booking-space-item',
    templateUrl: './item.component.html',
    styleUrls: ['./item.component.scss'],
})
export class BookingSpaceItemComponent extends BaseDirective {
    /** Space to display */
    @Input() public space: Space;
    /** Form fields for booking */
    @Input() public form: FormGroup;
    /** Whether multiple spaces are able to be selected */
    @Input() public multi: boolean;
    /** Emitter for select actions on the */
    @Output() public select = new EventEmitter<void>();

    /** Whether space image should be displayed */
    public get show_image() {
        return this._service.setting('app.space_display.show_images');
    }

    /** Whether this space is selected */
    public get selected(): boolean {
        /* istanbul ignore else */
        if (!this.form && this.space) {
            return false;
        }
        return (
            this.form.controls.space_list &&
            !!(this.form.controls.space_list.value || []).find(
                (space) => space.id === this.space.id
            )
        );
    }

    /** Whether space is booked by request */
    public get is_request(): boolean {
        /* istanbul ignore else */
        if (!this.form) {
            return false;
        }
        return this.space.byRequest({
            date: this.form.controls.date.value,
            duration: this.form.controls.duration.value,
            host: this.form.controls.organiser.value || this._users.current,
        });
    }

    public get availability(): string {
        const count = this.space.recurr_available.reduce(
            (count, ocurrence) => count + (ocurrence.available ? 1 : 0),
            0
        );
        return count >= this.space.recurr_available.length
            ? 'all'
            : `${count} of ${this.space.recurr_available.length}`;
    }

    /** Display location of the space */
    public get location(): string {
        /* istanbul ignore else */
        if (!this.space) {
            return 'Unable to determine location';
        }
        const level = this.space.level;
        const bld = this._org.buildings.find((building) => building.id === level.building_id);
        return `${bld ? bld.name + ', ' : ''}${level.name}`;
    }

    constructor(
        private _service: ApplicationService,
        private _org: OrganisationService,
        private _users: UsersService,
        private _dialog: MatDialog
    ) {
        super();
    }

    public selectSpace() {
        if (!this.space.recurr_available || this.availability === 'all' || this.selected) {
            this.select.emit();
        } else {
            const ref = this._dialog.open<
                RecurrenceSpacesModalComponent,
                RecurrenceSpacesModalData
            >(RecurrenceSpacesModalComponent, {
                width: 'auto',
                height: 'auto',
                maxWidth: '95vw',
                maxHeight: '95vh',
                data: {
                    space: this.space,
                    duration: this.form.controls.duration.value,
                    host: this.form.controls.organiser.value,
                    conflicts: this.form.controls.recurrence.value.conflicts || [],
                },
            });
            this.subscription(
                'recurr_conflicts',
                ref.componentInstance.event.subscribe((event: DialogEvent) => {
                    /* istanbul ignore else */
                    if (event.reason === 'done') {
                        const recurr: BookingRecurrenceDetails = this.form.controls.recurrence
                            .value;
                        const conflict_list: BookingRecurrenceSpaceDetails[] =
                            recurr.conflicts || [];
                        for (const item of event.metadata) {
                            const index = conflict_list.findIndex(
                                (event) =>
                                    event.date === item.date && event.replaces === item.replaces
                            );
                            if (index >= 0) {
                                conflict_list.splice(index, 1, item);
                            } else {
                                conflict_list.push(item);
                            }
                        }
                        this.form.controls.recurrence.setValue({
                            ...recurr,
                            conflicts: conflict_list,
                        });
                        this.select.emit();
                        ref.close();
                    }
                })
            );
        }
    }
}
