import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';

import { Booking } from 'src/app/services/data/bookings/booking.class';
import { BaseDirective } from 'src/app/shared/base.directive';
import { ApplicationService } from 'src/app/services/app.service';

import * as dayjs from 'dayjs';
import { OrganisationService } from 'src/app/services/data/organisation/organisation.service';
import { SpacesService } from 'src/app/services/data/spaces/spaces.service';
import { first } from 'rxjs/operators';

@Component({
    selector: 'day-view-approvals-view',
    templateUrl: './approvals-view.component.html',
    styleUrls: ['./approvals-view.component.scss'],
})
export class DayViewApprovalsComponent extends BaseDirective implements OnInit, OnChanges {
    /** Timestamp of the active date */
    @Input() public date: number;
    /** ID of the active level */
    @Input() public level: string;
    /** List of bookings that are pending approval */
    public events: Booking[] = [];
    /** List of spaces to check for pending approvals */
    public space_list: string[] = [];
    /** Whether the pending bookings are being loaded */
    public loading: boolean;
    /** Whether an event action is in progress */
    public waiting_on_event: boolean;

    /** Display value for the active date */
    public get date_display(): string {
        const now = dayjs().startOf('d');
        const date = dayjs(this.date).startOf('d');
        if (now.isSame(date, 'd')) {
            return 'Today';
        } else if (date.diff(now, 'd') === 1) {
            return 'Tomorrow';
        } else if (now.diff(date, 'd') === 1) {
            return 'Yesterday';
        }
        return date.format('DD MMM YYYY');
    }

    constructor(
        private _service: ApplicationService,
        private _org: OrganisationService,
        private _spaces: SpacesService
    ) {
        super();
    }

    public ngOnInit(): void {
        this._spaces.initialised.pipe(first((_) => _)).subscribe(() => this.init());
    }

    public init() {
        this.events = [];
        const building = this._org.building;
        this.space_list = this._spaces
            .filter(
                (space) =>
                    space.zones.indexOf(this.level === '' ? building.id : this.level) >= 0
            )
            .map((i) => i.id);
        this.processMeetings();
        this.getMonthlyPending();
        this.interval('monthly_pending', () => this.getMonthlyPending(), 10 * 60 * 1000);
    }

    public ngOnChanges(changes: SimpleChanges) {
        /* istanbul ignore else */
        if (changes.date && this.date !== changes.date.previousValue) {
            this.events = [];
            this.updateMeetings();
            // this.interval('update_meetings', () => this.updateMeetings(), 60 * 1000);
            this.processMeetings();
            this.interval('process_meetings', () => this.processMeetings(), 1 * 1000);
            /* istanbul ignore else */
            if (!dayjs(changes.date.previousValue).isSame(dayjs(changes.date.currentValue), 'M')) {
                this.getMonthlyPending();
                this.interval('monthly_pending', () => this.getMonthlyPending(), 10 * 60 * 1000);
            }
        }
        if (changes.level) {
            this.init();
        }
    }

    /* istanbul ignore next */
    /**
     * Track by function for ngFor optimisation
     * @param index Index of the item
     * @param item Data associated with the item at the given index
     */
    public trackByFn(index: number, item: Booking) {
        return item.id || index;
    }

    /** Update listing of meetings pending approval */
    private updateMeetings() {
        const time = dayjs(this.date).startOf('d');
        const end = time.endOf('d');
        this.loading = true;
        const building = this._org.building;
        this._spaces
            .query({
                zone_ids: this.level || building.id,
                available_from: time.unix(),
                available_to: end.unix(),
            })
            .then((room_list) => {
                this.space_list = room_list.map((i) => i.id);
                this.processMeetings();
                this.loading = false;
            });
    }

    /**
     * Update pending meetings from list of rooms
     */
    private processMeetings() {
        /* istanbul ignore if */
        if (this.waiting_on_event) {
            return;
        }
        this.events = this.process(this.date);
        const pending_list = this._service.get('CONCIERGE.pending_bookings') || {};
        pending_list[dayjs(this.date).format('YYYY-MM-DD')] = this.events.length;
        this._service.set('CONCIERGE.pending_bookings', pending_list);
    }

    private process(date: number) {
        let events: Booking[] = [];
        for (const id of this.space_list) {
            const space = this._spaces.find(id);
            /* istanbul ignore else */
            if (space) {
                events = events.concat(
                    space.bookingsFor(date).filter((booking) => booking.tentative)
                );
            }
        }
        return events.sort((a, b) => a.date - b.date);
    }

    private getMonthlyPending() {
        const now = dayjs().startOf('d');
        let start = dayjs(this.date).startOf('M');
        const end = start.endOf('M');
        /* istanbul ignore else */
        if (now.isAfter(end, 'm')) {
            return;
        } else if (now.isAfter(start, 'm')) {
            start = now;
        }
        const building = this._org.building;
        this._spaces
            .query({
                zone_ids: this.level || building.id,
                available_from: start.unix(),
                available_to: end.unix(),
            })
            .then((_) => {
                const pending_list = this._service.get('CONCIERGE.pending_bookings') || {};
                for (let date = start; date.isBefore(end, 'm'); date = date.add(1, 'd')) {
                    const events = this.process(date.valueOf());
                    pending_list[date.format('YYYY-MM-DD')] = events.length;
                }
                this._service.set('CONCIERGE.pending_bookings', pending_list);
            });
    }
}
