import { CateringItem } from './catering-item.class';
import { HashMap } from '../../../shared/utilities/types.utilities';

import { CateringCategory } from './catering-category.class';
import { Booking } from '../bookings/booking.class';

import * as dayjs from 'dayjs';

export type CateringOrderMutableProperties =
    | 'items'
    | 'location_id'
    | 'location'
    | 'delivery_time'
    | 'charge_code'
    | 'notes'
    | 'status';

export type CateringOrderStatus = 'accepted' | 'preparing' | 'ready' | 'delivered' | 'cancelled';

export class CateringOrder {
    /** Booking associated with the order */
    public booking: Booking;
    /** Whether the order status change is loading */
    public loading: boolean;
    /** Unique ID for the catering order */
    readonly id: string;
    /** Unique ID for the catering order */
    readonly booking_id: string;
    /** Number of minutes after the start of the associated meeting to delivery the order */
    readonly booking_date: number = dayjs()
        .startOf('m')
        .valueOf();
    /** Number of minutes after the start of the associated meeting to delivery the order */
    readonly delivery_time: number = 0;
    /** ID of the location to deliver the items to */
    readonly location_id: string = '';
    /** Display text for the location to deliver the items to */
    readonly location: string = '';
    /** Status of the catering order */
    readonly status: CateringOrderStatus;
    /** Whether items in the order need to be prepared in the kitchen */
    readonly kitchen: boolean;
    /** Whether items in the order are in the pantry */
    readonly pantry: boolean;
    /** List of items */
    readonly items: readonly (CateringItem | CateringCategory)[] = [];
    /** Creation time of the order */
    readonly created_at: number;
    /** Charge code associated with the order */
    readonly charge_code: string;
    /** Notes associated with the order */
    readonly notes: string;
    /** Mapping of properties to their changes */
    private _changes: HashMap = {};
    /** Map of local property names to server ones */
    protected _server_names: HashMap<string> = {};
    /** Currency code for the location of the order */
    public symbol: string;
    /** Whether the is an error with the order */
    public error: boolean;

    constructor(data: HashMap) {
        this.id = data.id || `order-${Math.floor(Math.random() * 999_999_999)}`;
        this.booking = data.booking;
        this.delivery_time = data.delivery_time || data.start || 0;
        this.booking_date = data.booking_date;
        this.location_id = data.location_id || data.location;
        this.location = data.location || '';
        this.status = data.status || 'accepted';
        this.charge_code = data.charge_code || data.code;
        this.notes = data.notes;
        this.kitchen = data.kitchen instanceof Array ? !!data.kitchen.length : data.kitchen;
        this.pantry = data.pantry instanceof Array ? !!data.pantry.length : data.pantry;
        this.symbol = data.symbol || 'USD';
        this.items = (data.items || [])
            .map(item => (item.items ? new CateringCategory(item) : new CateringItem(item)))
            .filter(item => !!item.amount);
    }

    /** Mapping of properties to their changes */
    public get changes(): HashMap<any> {
        return { ...this._changes } || {};
    }

    /** Display string for the types of items in the order */
    public get type(): string {
        let type = '';
        /* istanbul ignore else */
        if (this.kitchen) type += 'Kitchen';
        /* istanbul ignore else */
        if (this.pantry) {
            /* istanbul ignore else */
            if (type) type += ' + ';
            type += 'Pantry';
        }
        return type;
    }

    /** Total cost of the order */
    public get total(): number {
        return this.items.reduce((total, item) => total + (item.total || 0), 0);
    }

    /** Total number of items in the order */
    public get item_count(): number {
        return this.items.reduce((total, item) => total + (item.amount || 0), 0);
    }

    /** Display string for the delivery time of the order */
    public get deliver_at_time(): string {
        const date = this.booking ? this.booking.date : this.booking_date;
        return dayjs(date)
            .add(this.delivery_time, 'm')
            .format('h:mm A');
    }

    /** Display string for the delivery date of the order */
    public get deliver_on_date(): string {
        const date = this.booking ? this.booking.date : this.booking_date;
        return dayjs(date)
            .add(this.delivery_time, 'm')
            .format('DD MMM YYYY');
    }

    /** Whether there are any pending changes to be saved */
    public get has_changes(): boolean {
        return !!Object.keys(this._changes).length;
    }

    /**
     * Store pending changes for the given property
     * @param name Property to update value
     * @param value New value to save for property
     */
    public storePendingChanges(name: CateringOrderMutableProperties, value: any): void {
        this._changes[name] = value;
    }

    /** Clear any changes waiting to be saved */
    public clearPendingChanges() {
        delete this._changes;
        this._changes = {};
    }

    /**
     * Convert class object into plain object
     */
    public toJSON(this: CateringOrder, with_changes: boolean = true): HashMap {
        const obj: any = { ...this };
        // Remove local private members
        delete obj._changes;
        delete obj._server_names;
        // Remove local public members
        delete obj.booking;
        delete obj.loading;

        // Convert remaining members to be public
        const keys = Object.keys(obj);
        for (const key of keys) {
            if (key[0] === '_') {
                const new_key = this._server_names[key.substr(1)] || key.substr(1);
                obj[new_key] = obj[key];
                delete obj[key];
            } else if (obj[key] === undefined) {
                delete obj[key];
            }
        }
        obj.items = obj.items.map((item: CateringItem) => item.toJSON());
        return with_changes ? { ...obj, ...this._changes } : obj;
    }
}
