import { BaseDataClass } from '../base-api.class';
import { HashMap, Identity } from '../../../shared/utilities/types.utilities';
import { MapLocation } from '../location/location.class';
import { ServiceManager } from '../service-manager.class';

export class User extends BaseDataClass {
    /** Currently logged in user */
    public static get active_user(): User {
        const service = ServiceManager.serviceFor(User);
        return service ? service.current : null;
    }
    /** First name of the user */
    public readonly first_name: string;
    /** Last name of the user */
    public readonly last_name: string;
    /** Windows user ID */
    public readonly windows_id: string;
    /** Type of user */
    public readonly type: 'partner' | 'external' | 'internal';
    /** Contact phone number of the user */
    public readonly phone: string;
    /** Organisational department the user is part of */
    public readonly business_unit: string;
    /** Whether user is external from the application's organisation */
    public readonly external: boolean;
    /** Organisational Identifier for the user */
    public readonly staff_code: string;
    /** Basic location descriptor for the user */
    public readonly location: string;
    /** List of users that this user can act on behalf */
    public readonly delegates: readonly string[];
    /** URL of the user's avatar image */
    public readonly image: string;
    /** List of user groups associated with this user */
    public readonly groups: readonly string[];
    /** Organisation the user is associated with */
    private _organisation: Identity;

    /** Map location of the user */
    private _location: MapLocation;
    /** List of desk bookings */
    private _desk_bookings: HashMap = {};

    constructor(raw_data: HashMap = {}) {
        super(raw_data);
        this.first_name = raw_data.first_name || '';
        this.last_name = raw_data.last_name || '';
        this.windows_id = raw_data.email || '';
        this.type = raw_data.visitor ? 'external' : raw_data.type || 'internal';
        this.phone = raw_data.phone || raw_data.mobile || '';
        this.business_unit = raw_data.business_unit || raw_data.department || '';
        this._organisation = raw_data.organisation || {
            id: raw_data.organisation_id,
            name: raw_data.organisation_name
        };
        this.external = this.type === 'external';
        this._location = null;
        this.image = raw_data.image || '';
        this.staff_code = raw_data.staff_code || raw_data.fmno || '';
        this.delegates = raw_data.delegates || [];
        this._location = new MapLocation(
            raw_data.location && raw_data.location instanceof Object
                ? raw_data.location
                : { name: raw_data.location }
        );
        this.groups = raw_data.groups || [];
        this.location = raw_data.location || raw_data.officeLocation || '';
    }

    /** Organisation the user is associated with */
    public get organisation() {
        return this._organisation;
    }

    /** Map location of the user */
    public get last_location(): MapLocation {
        return new MapLocation(this._location || {})
    }

    /**
     * Ask the server for the users current location
     */
    public locate(): Promise<MapLocation> {
        const service: any = ServiceManager.serviceFor(MapLocation);
        return new Promise((resolve, reject) => {
            if (service) {
                service.show(this.id, { desk: this.email }).then(
                    l => {
                        this._location = l;
                        resolve(l);
                    },
                    e => reject(e)
                );
            } else {
                resolve(null);
            }
        });
    }

    /**
     * Whether user is available during the given period
     * @param start Start time as a unix timestamp with milliseconds
     * @param end End time as a unix timestamp with milliseconds
     */
    public available(start?: number, end?: number): Promise<boolean> {
        return new Promise<boolean>(resolve => {
            this._service.query({ email: this.email, start, end }).then(
                l => resolve(l && l.length > 0),
                _ => resolve(false)
            );
        });
    }

    /**
     * Make a copy of this object
     */
    public clone(): User {
        return new User(this);
    }

    /**
     * Make a copy of this object without identification data
     */
    public duplicate(): User {
        return new User({ ...this, id: null, email: null });
    }

    /**
     * Convert object into plain object
     */
    public toJSON(this: User): HashMap {
        const data = super.toJSON();
        delete data.location;
        return data;
    }
}
