import { Component, Input } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';

import { Booking } from 'src/app/services/data/bookings/booking.class';
import { MeetingDetailsModalComponent } from 'src/app/overlays/meeting-details-modal/meeting-details-modal.component';

@Component({
    selector: 'week-view-item',
    templateUrl: './item.component.html',
    styleUrls: ['./item.component.scss']
})
export class WeekViewItemComponent {
    @Input() public event: Booking;

    /** Type of booking */
    public get type(): string {
        if (this.event.declined) {
            return 'cancelled';
        }
        const booking_type = this.event.type;
        if (booking_type === 'internal' && this.event.has_visitors) {
            return 'external';
        }
        return booking_type;
    }

    constructor(private _dialog: MatDialog) {}

    /**
     * Open modal to view bookings details
     */
    public viewMeetingDetails() {
        this._dialog.open(MeetingDetailsModalComponent, {
            maxWidth: 'calc(100vw - 2em)',
            data: {
                booking: this.event
            }
        })
    }
}
