import { Injectable } from '@angular/core';
import { Location } from '@angular/common';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { ComposerService } from '@placeos/composer';
import { first } from 'rxjs/operators';

import { Md5 } from 'ts-md5/dist/md5';

import { BaseAPIService } from '../base.service';
import { User } from './user.class';
import { HashMap, ApplicationLoadingState } from '../../../shared/utilities/types.utilities';
import { unique } from 'src/app/shared/utilities/general.utilities';

import * as dayjs from 'dayjs';
import { ApplicationService } from '../../app.service';
import { ServiceManager } from '../service-manager.class';

export interface ILoginOptions {
    /** URL to post login request to */
    url?: string;
    /** Whether form data is passed in the body of the request */
    form?: boolean;
    /** Custom headers to pass into the post request */
    headers?: HashMap<string>;
}

@Injectable({
    providedIn: 'root',
})
export class UsersService extends BaseAPIService<User> {
    constructor(
        protected _composer: ComposerService,
        protected uhttp: HttpClient,
        protected location: Location,
        private _service: ApplicationService
    ) {
        super(_composer);
        ServiceManager.setService(User, this);
        this._name = 'Users';
        this._api_route = '/users';
        this._compare = (a, b) => !a.id.localeCompare(b.id) || !a.email.localeCompare(b.email);
        this.set('current_user', new User({ id: 'local_user', name: 'Local User' }));
        this._composer.initialised.pipe(first((_) => _)).subscribe(() => this.init());
    }

    /** Currently logged in user */
    public get current(): User {
        return this.get('current_user');
    }

    /** Whether the user has properly logged in */
    public get is_logged_in(): boolean {
        return this.current && this.current.id !== 'local_user';
    }

    /** istanbul ignore function */
    /**
     * Sets the access token and expiry for the user
     * @param token OAuth bearer token
     * @param expiry Expiry epoch timestamp in ms
     * @param reload Whether to reload the page after setting tokens
     */
    public setToken(token: string, expiry?: number, reload: boolean = true) {
        if (!expiry) {
            expiry = dayjs().add(7, 'd').endOf('d').valueOf();
        }
        const path = `${location.origin}${
            this._service.setting('composer.route') || ''
        }/oauth-resp.html`;
        const client_id = Md5.hashStr(path);
        if (sessionStorage) {
            sessionStorage.setItem(`${client_id}_access_token`, token);
            sessionStorage.setItem(`${client_id}_expires_at`, `${expiry}`);
        }
        if (localStorage) {
            localStorage.setItem(`${client_id}_access_token`, token);
            localStorage.setItem(`${client_id}_expires_at`, `${expiry}`);
        }
        if (reload) {
            this.location.go(this.location.path());
        }
        return path;
    }

    /**
     * Post login request to the API
     * @param query_params Key value pairs to pass into
     * @param options Options for the login request
     */
    public login(query_params: HashMap, options: ILoginOptions = {}): Promise<void> {
        if (!this._promises['login']) {
            this._promises['login'] = new Promise<void>((resolve, reject) => {
                this.set('status', 'loading');
                let headers = new HttpHeaders();
                if (!options || options.form !== false) {
                    headers = headers.append('Content-Type', 'application/x-www-form-urlencoded');
                }
                const url = (options ? options.url : '') || '/auth/signin';
                this.uhttp.post(url, query_params, { headers }).subscribe(
                    (_) => null,
                    (e) => reject(e),
                    () => {
                        this._composer.auth.authorise();
                        resolve();
                    }
                );
            });
        }
        return this._promises['login'];
    }

    /**
     * Logout of user and redirect to logout URL
     */
    public logout() {
        this._composer.auth.logout();
    }

    /**
     * Load initial data for the service
     */
    protected load(tries: number = 0): Promise<void> {
        const loading: ApplicationLoadingState = this._service.get('loading') || {};
        if (!loading.user) {
            loading.user = { message: 'Loading user credentials', state: 'loading' };
            this._service.set('loading', loading);
        }
        this.set('status', 'loading');
        return new Promise<void>((resolve) => {
            if (tries > 4) {
                this.set('status', 'invalid');
                loading.user = { message: 'Loading user credentials', state: 'failed' };
                this._service.set('loading', loading);
                return resolve();
            }
            this.show('current').then(
                (current_user) => {
                    this.set('status', 'available');
                    this.set('current_user', current_user);
                    this._initialised.next(true)
                    if (this._service && this._service.setting('app.user.grab_api_details')) {
                        this.show(current_user.email).then(
                            (user) => {
                                this.set('current_user', user);
                                if (this._service.setting('app.user.update_location')) {
                                    this.interval('location', () => user.locate(), 5000);
                                }
                                this.loadDelegates();
                                resolve();
                                loading.user = {
                                    message: 'Loading user credentials',
                                    state: 'complete',
                                };
                                this._service.set('loading', loading);
                            },
                            () =>
                                this.timeout('load', () => this.load(++tries).then(() => resolve()))
                        );
                    } else {
                        if (this._service.setting('app.user.update_location')) {
                            this.interval('location', () => current_user.locate(), 5000);
                        }
                        this.loadDelegates();
                        resolve();
                        loading.user = { message: 'Loading user credentials', state: 'complete' };
                        this._service.set('loading', loading);
                    }
                },
                () => this.timeout('load', () => this.load(++tries).then(() => resolve()))
            );
        });
    }

    private async loadDelegates() {
        const delegates = this.current.delegates;
        if (!delegates || !delegates.length) {
            return [];
        }
        const promises: Promise<User>[] = [];
        for (const email of delegates) {
            promises.push(this.show(email));
        }
        const list = await Promise.all(promises);
        this.set('delegates', list);
        this.set('list', unique((this.get('list') || []).concat(list)));
        return list;
    }

    /**
     * Convert raw API data into a valid User Object
     * @param raw_item Raw API data
     */
    protected process(raw_data: any): User {
        return new User(raw_data);
    }
}
