import {
    Component,
    OnInit,
    OnChanges,
    HostListener,
    ViewChild,
    ElementRef,
    Renderer2,
    Input,
    SimpleChanges,
} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';
import { first } from 'rxjs/operators';

import { BaseDirective } from 'src/app/shared/base.directive';
import { Space } from 'src/app/services/data/spaces/space.class';
import { ApplicationService } from 'src/app/services/app.service';
import { Booking } from 'src/app/services/data/bookings/booking.class';
import { Identity, HashMap } from 'src/app/shared/utilities/types.utilities';
import { MeetingDetailsModalComponent } from 'src/app/overlays/meeting-details-modal/meeting-details-modal.component';
import { SpacesService } from 'src/app/services/data/spaces/spaces.service';
import { OrganisationService } from 'src/app/services/data/organisation/organisation.service';

import * as dayjs from 'dayjs';

const UPDATE_INTERVAL = 30;

@Component({
    selector: 'day-view-timeline',
    templateUrl: './timeline.component.html',
    styleUrls: ['./timeline.component.scss'],
})
export class DayViewTimelineComponent extends BaseDirective implements OnInit, OnChanges {
    /** Timestamp of the active date */
    @Input() public date: number;
    /** ID of the active level */
    @Input() public level: string;
    /** ID of the room type to filter displayed spaces by */
    @Input() public space_type: string;
    /** List of shown legend items */
    @Input() public legend: string[] = [];
    /** Whether to show only items with a setup and/or breakdown time */
    @Input() public overflow_only: boolean;
    /** Whether space bookings are being loaded */
    @Input() public loading: boolean;
    /** List of timeblocks to display */
    public time_blocks: Identity[] = this.generateTimeBlocks();
    /** Top value of the content scroll */
    public scroll_top: number = 0;
    /** Left value of the content scroll */
    public scroll_left: number = 0;
    /** Offset of the scroll value when starting a drag */
    public scroll_offset: number = 0;
    /** List of spaces to display for  */
    public spaces: Space[] = [];
    /** List of spaces to display for  */
    public filtered_spaces: Space[] = [];
    /** Mapping of space ids to whether they are in view */
    public in_view: { [id: string]: boolean } = {};
    /** Percent of the day that is now */
    public now: number;
    /** Whether the active day is today */
    public is_today: boolean;
    /** Currently active hour of the day */
    public active_hour: string;
    /** Mapping of spaces loading booking date */
    public loading_space: { [id: string]: boolean } = {};
    /** Booking ID to open modal for */
    public view_id: string;
    /** Last update time for the space bookings */
    public last_update: number = 0;
    /** Whether an event is being dragged */
    public is_dragging: boolean;
    /** Client rectangle for the scroll area */
    public box: ClientRect;
    /** Whether to show the holding bay */
    public show_holding_bay: boolean = true;

    /** Scrollable area of the day view */
    @ViewChild('scroll_area', { static: true }) private scroll_area: ElementRef<HTMLDivElement>;

    /** Update scroll box when the window is resized */
    @HostListener('window:resize') public onResize() {
        if (this.scroll_area.nativeElement) {
            this.box = this.scroll_area.nativeElement.getBoundingClientRect();
        }
    }

    public get legend_map(): HashMap<boolean> {
        const map: any = {};
        this.legend.forEach((item) => (map[item] = true));
        return map;
    }

    public get is_loading(): boolean {
        return (
            this.loading ||
            Object.keys(this.loading_space).reduce(
                (loading, key) => loading || this.loading_space[key],
                false
            )
        );
    }

    constructor(
        private _service: ApplicationService,
        private _spaces: SpacesService,
        private _org: OrganisationService,
        private _route: ActivatedRoute,
        private _renderer: Renderer2,
        private _dialog: MatDialog
    ) {
        super();
    }

    public get holding_bay(): Space {
        const bld = this._org.building;
        /* istanbul ignore else */
        if (bld && bld.holding_bay) {
            return this._spaces.find(bld.holding_bay);
        }
        return null;
    }

    public ngOnInit(): void {
        this._spaces.initialised.pipe(first((_) => _)).subscribe(() => {
            this._org.initialised.pipe(first((_) => _)).subscribe(() => {
                const zone_id = !this.level ? this._org.building.id : this.level;
                this.spaces = this._spaces.filter((_) => _.zones.indexOf(zone_id) >= 0);
                this.init();
                this.initSpaces();
                // Update time
                this.updateTime();
                this.interval('time', () => this.updateTime(), 15 * 1000);
            });
        });
    }

    public ngOnChanges(changes: SimpleChanges): void {
        /* istanbul ignore else */
        if (changes.date) {
            this.is_today = dayjs().isSame(dayjs(this.date), 'd');
            this.updateBookings();
            this.interval('bookings', () => this.updateBookings(), UPDATE_INTERVAL * 1000);
        }
        /* istanbul ignore else */
        if (changes.level && this._org.building) {
            const zone_id = !this.level ? this._org.building.id : this.level;
            this.spaces = this._spaces.filter((_) => _.zones.indexOf(zone_id) >= 0);
            this.initSpaces();
            this.updateBookings();
            this.interval('bookings', () => this.updateBookings(), UPDATE_INTERVAL * 1000);
        }
        /* istanbul ignore else */
        if (changes.space_type) {
            this.initSpaces();
        }
    }

    public init() {
        this._service.title = 'Day View';
        this.subscription(
            'viewing',
            this._service
                .listen('CONCIERGE.day_view.viewing')
                .subscribe((event) => this.scrollIntoView(event))
        );
        // Subscribe to route query changes
        this.subscription(
            'route.query',
            this._route.queryParamMap.subscribe((params) => {
                /* istanbul ignore else */
                if (params.has('view')) {
                    this.view_id = params.get('view');
                    const details = localStorage.getItem('CONCIERGE.view_booking');
                    /* istanbul ignore else */
                    if (details) {
                        const booking = new Booking(JSON.parse(details));
                        this.view(booking);
                        localStorage.removeItem('CONCIERGE.view_booking');
                    }
                }
            })
        );
        // Update bookings for visible rooms
        this.updateBookings();
        this.interval('bookings', () => this.updateBookings(), UPDATE_INTERVAL * 1000);
        this.interval(
            'scroll',
            () => {
                /* istanbul ignore else */
                if (this.scroll_area && this.scroll_area.nativeElement) {
                    this.updateScroll({ target: this.scroll_area.nativeElement });
                }
            },
            1000
        );
        /* istanbul ignore else */
        if (this.scroll_area.nativeElement) {
            this.box = this.scroll_area.nativeElement.getBoundingClientRect();
        }
    }

    /** List of space */
    public get space_zones(): string[] {
        return this.spaces.map((i) => `space-col-${i.id}`);
    }

    /**
     * Update scroll parameters to match the change in the scroll position
     * @param event
     */
    public updateScroll(event) {
        this.scroll_top = event.target.scrollTop;
        this.scroll_left = event.target.scrollLeft;
    }

    /** Generate time blocks to display on the day view */
    private generateTimeBlocks(): Identity[] {
        const blocks: Identity[] = [];
        const date = dayjs().startOf('h');
        for (let i = 0; i < 24; i++) {
            blocks.push({
                id: date.hour(i).format('HH:mm'),
                name: date.hour(i).format('h:mm A'),
            });
        }
        return blocks;
    }

    /** Update the now line position */
    private updateTime() {
        const time = dayjs();
        this.now = (time.hour() + time.minute() / 60) / 24;
        this.active_hour = time.startOf('h').format('HH:mm');
    }

    /** Update bookings for rooms in view */
    private updateBookings() {
        const visible_rooms = this.spaces.filter((i) => this.in_view[i.id] !== false);
        if (visible_rooms.length) {
            visible_rooms.forEach((rm) => (this.loading_space[rm.id] = true));
            const time = dayjs(this.date).startOf('d');
            const end = time.endOf('d');
            const room_ids = visible_rooms.reduce((v, i) => v + (v ? ',' : '') + i.id, '');
            const level = this._service.get('APP.level');
            const zone_ids = [level === -1 ? this._org.building.id : level];
            /* istanbul ignore else */
            if (this.space_type) {
                zone_ids.push(this.space_type);
            }
            this.loading = true;
            this._spaces
                .query({
                    room_ids,
                    zone_ids: zone_ids.join(','),
                    available_from: time.unix(),
                    available_to: end.unix(),
                })
                .then(
                    (room_list) => {
                        room_list.forEach((rm) => {
                            /* istanbul ignore else */
                            if (this.view_id) {
                                const booking = rm.bookings.find((i) => i.id === this.view_id);
                                /* istanbul ignore else */
                                if (booking) {
                                    this.view(booking);
                                }
                            }
                            const space = this.filtered_spaces.find((space) => space.id === rm.id);
                            /* istanbul ignore else */
                            if (space) {
                                (space as any).bookings = (rm as any).bookings;
                            }
                        });

                        delete this.loading_space;
                        this.last_update = dayjs().valueOf();
                        this.loading_space = {};
                        this.loading = false;
                    },
                    () => {
                        delete this.loading_space;
                        this.loading_space = {};
                        this.loading = false;
                    }
                );
        }
    }

    /**
     * View booking details
     * @param booking Booking to view the details of
     */
    private view(booking: Booking) {
        this._dialog.open(MeetingDetailsModalComponent, {
            data: {
                booking,
            },
        });
        this.view_id = null;
    }

    /**
     * Scroll booking to the event on the day view
     * @param booking Booking to scroll into view
     */
    private scrollIntoView(booking: Booking) {
        if (booking && this.scroll_area && this.scroll_area.nativeElement) {
            const element: HTMLDivElement = this.scroll_area.nativeElement.querySelector(
                `#event-${booking.id}`
            );
            /* istanbul ignore else */
            if (element) {
                const scroll_box = this.scroll_area.nativeElement.getBoundingClientRect();
                const box = element.getBoundingClientRect();
                this.scroll_area.nativeElement.scrollTo({
                    left: box.left - scroll_box.left + this.scroll_area.nativeElement.scrollLeft,
                    top: box.top - scroll_box.top + this.scroll_area.nativeElement.scrollTop,
                    behavior: 'smooth',
                });
            }
        }
    }

    /**
     * Setup an auto-scroll for the display view
     * @param x
     * @param y
     */
    public scrollView(x: number, y: number) {
        /* istanbul ignore else */
        if (x || y) {
            this.scroll_area.nativeElement.scrollTop += y || 0;
            this.scroll_area.nativeElement.scrollLeft += x || 0;
            /* istanbul ignore else */
            if (
                (y &&
                    this.scroll_area.nativeElement.scrollTop >=
                        this.scroll_area.nativeElement.scrollHeight) ||
                (x &&
                    this.scroll_area.nativeElement.scrollLeft >=
                        this.scroll_area.nativeElement.scrollWidth) ||
                (y && this.scroll_area.nativeElement.scrollTop <= 0) ||
                (x && this.scroll_area.nativeElement.scrollLeft <= 0)
            ) {
                this.clearInterval('auto_scroll');
            }
            this.updateScroll({ target: this.scroll_area.nativeElement });
        }
    }

    public clearSelected() {
        this._service.set('CONCIERGE.day_view.viewing', null);
    }

    public cancelScroll() {
        this.clearInterval('auto_scroll');
    }

    public dragChange(is_dragging: boolean) {
        if (is_dragging) {
            this.scroll_offset = this.scroll_area.nativeElement.scrollTop;
            this.subscription(
                'listen_move',
                this._renderer.listen('window', 'mousemove', (event: MouseEvent) => {
                    const position = { x: event.clientX, y: event.clientY };
                    const y =
                        position.y > this.box.bottom - this.box.height * 0.05
                            ? 1
                            : position.y < this.box.top + this.box.height * 0.05
                            ? -1
                            : 0;
                    const x =
                        position.x > this.box.right - this.box.width * 0.05
                            ? 1
                            : position.x < this.box.left + this.box.height * 0.05
                            ? -1
                            : 0;
                    this.scrollView(x * 5, y * 5);
                    this.interval('auto_scroll', () => this.scrollView(x * 10, y * 10), 10);
                })
            );
            this.clearInterval('bookings');
        } else {
            this.interval('bookings', () => this.updateBookings(), UPDATE_INTERVAL * 1000);
            this.cancelScroll();
            this.unsub('listen_move');
        }
    }

    private initSpaces() {
        const holding_bay = this.holding_bay;
        this.filtered_spaces = this.spaces.filter(
            (i) =>
                (!this.space_type || i.zones.indexOf(this.space_type) >= 0) &&
                (!holding_bay || i.id !== holding_bay.id) &&
                i.bookable
        );
        this.filtered_spaces.sort((a, b) => a.name.localeCompare(b.name));
    }
}
