import { Component, OnInit, Input } from '@angular/core';

import { Booking } from 'src/app/services/data/bookings/booking.class';
import { CateringOrder } from 'src/app/services/data/catering/catering-order.class';
import { ApplicationService } from 'src/app/services/app.service';
import { BookingModalComponent } from '../../booking-modal/booking-modal.component';
import { MatDialog } from '@angular/material/dialog';
import { OrganisationService } from 'src/app/services/data/organisation/organisation.service';

@Component({
    selector: 'meeting-catering',
    templateUrl: './catering.component.html',
    styleUrls: ['./catering.component.scss'],
})
export class MeetingCateringDetailsComponent {
    /** Booking to display details */
    @Input() public booking: Booking;

    /** List of orders for this booking and space */
    public get orders(): CateringOrder[] {
        return this.booking.catering.filter((order) => order.location_id === this.booking.space.id);
    }

    /** Total cost of all the orders */
    public get total(): number {
        let total = 0;
        this.orders.forEach((order) => (total += order.total));
        return total;
    }

    /** Charge code for orders in the active space */
    public get charge_code(): string {
        return (this.orders.length ? this.orders[0].charge_code : '') || '<No Charge code>';
    }

    /** Currency symbol for orders in active space */
    public get symbol(): string {
        const space = (this.booking.space || { level: { building_id: '' } });
        const building = this._org.buildings.find(
            (bld) => bld.id === space.level.building_id
        ) || { currency: 'USD' };
        return building.currency;
    }

    /** Notes for orders in active space */
    public get notes(): string {
        const note = this.booking.notes.find(
            (note) =>
                note.type === 'catering' &&
                note.space === this.booking.space.id &&
                note.author === this.booking.creator.name
        );
        return note ? note.message : '<No Notes>';
    }

    /** Whether booking has been declined or ended */
    public get expired(): boolean {
        return this.booking.status === 'done' || this.booking.declined;
    }

    constructor(private _org: OrganisationService, private _dialog: MatDialog) {}

    /** Store the order details for viewing in another component */
    public storeOrder(order: CateringOrder) {
        localStorage.setItem('CONCIERGE.view_order', JSON.stringify(order.toJSON()));
        localStorage.setItem('CONCIERGE.view_booking', JSON.stringify(this.booking.toJSON()));
        localStorage.setItem('CONCIERGE.date', `${this.booking.date}`);
    }

    /** Open modal to edit catering details for booking */
    public editMeetingCatering() {
        this._dialog.open(BookingModalComponent, {
            data: {
                booking: this.booking,
                only_catering: true,
            },
        });
    }
}
