import { Component, OnInit, Input, Output, EventEmitter, ViewChild, ChangeDetectorRef } from '@angular/core';
import { MatMenuTrigger } from '@angular/material/menu';
import { first } from 'rxjs/operators';

import { ApplicationService } from 'src/app/services/app.service';
import { BaseDirective } from '../../base.directive';

import * as dayjs from 'dayjs';

@Component({
    selector: 'a-topbar-header',
    templateUrl: './topbar-header.component.html',
    styleUrls: ['./topbar-header.component.scss']
})
export class TopbarHeaderComponent extends BaseDirective implements OnInit {
    /** Currently selected date */
    @Input() public date: number;
    /** Whether to show new item button */
    @Input() public show_add_item = false;
    /** Whether to show the events for the active month */
    @Input() public show_events: boolean = false;
    /** Emitter for changes selected date */
    @Output() public dateChange = new EventEmitter<number>();
    /** Emitter for add item button */
    @Output() public event = new EventEmitter();
    /** Counters of the events on given dates. Key format is `YYYY-MM-DD` */
    public event_map: { [date: string]: number };

    @ViewChild(MatMenuTrigger, { static: true }) trigger: MatMenuTrigger;

    /** Display string for the currently selected date */
    public get date_display(): string {
        const date = dayjs(this.date);
        return !date.isSame(dayjs(), 'd') ? date.format('DD MMM YYYY') : '';
    }

    constructor(private _service: ApplicationService, private _cdr: ChangeDetectorRef) {
        super();
    }

    public ngOnInit(): void {
        this._service.initialised.pipe(first(_ => _)).subscribe(() => {
            const date = +(localStorage.getItem('CONCIERGE.date'));
            /* istanbul ignore else */
            if (date && date !== this.date) {
                this.timeout('init', () => {
                    this.date = date;
                    this.dateChange.emit(this.date);
                }, 10)
            }
            this.subscription('events', this._service.listen('CONCIERGE.pending_bookings').subscribe((value) => {
                this.event_map = { ...value } || {};
                this._cdr.detectChanges();
            }));
        });
    }

    /** Change the selected date */
    public changeDate(value: number) {
        let date = dayjs(this.date).startOf('d');
        date = date.add(value, 'd');
        this.date = date.valueOf();
        this.dateChange.emit(this.date);
        localStorage.setItem('CONCIERGE.date', `${this.date}`);
    }

    /** Reset date to the current date */
    public resetDate() {
        this.date = dayjs().startOf('d').valueOf();
        this.dateChange.emit(this.date);
        localStorage.setItem('CONCIERGE.date', `${this.date}`);
    }

    public closeDatePicker() {
        this.trigger.closeMenu();
    }

    /** New Item button click */
    public newItem(): void {
        this.event.emit('new_item');
    }
}
