import { Component, Inject, Output, EventEmitter } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialog, MatDialogRef } from '@angular/material/dialog';

import { Booking } from 'src/app/services/data/bookings/booking.class';
import { ApplicationService } from 'src/app/services/app.service';
import {
    ConfirmModalComponent,
    ConfirmModalData,
    CONFIRM_METADATA,
} from '../confirm-modal/confirm-modal.component';
import { DialogEvent } from 'src/app/shared/utilities/types.utilities';
import { BaseDirective } from 'src/app/shared/base.directive';
import { BookingsService } from 'src/app/services/data/bookings/bookings.service';
import { DeleteConfirmModalComponent } from '../delete-confirm-modal/delete-confirm-modal.component';

export interface MeetingDetailsModalData {
    booking: Booking;
}

@Component({
    selector: 'a-meeting-details-modal',
    templateUrl: './meeting-details-modal.component.html',
    styleUrls: ['./meeting-details-modal.component.scss'],
})
export class MeetingDetailsModalComponent extends BaseDirective {
    /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** Display string for the loading state */
    public loading: string;
    /** Handler for request errors */
    private _on_error = (message) => (err) => {
        this._service.notifyError(`${message}. Error: ${err.message || err}`);
        this.setLoading(null);
    };
    /** Booking to view details */
    public get booking(): Booking {
        return this._data.booking;
    }
    /** Type of meeting */
    public get type(): string {
        return this.booking.declined ? 'declined' : this.booking.type;
    }

    constructor(
        private _service: ApplicationService,
        private _bookings: BookingsService,
        private _dialog_ref: MatDialogRef<MeetingDetailsModalComponent>,
        private _dialog: MatDialog,
        @Inject(MAT_DIALOG_DATA) private _data: MeetingDetailsModalData
    ) {
        super();
    }

    public setLoading(state: string) {
        this.loading = state;
        this._dialog_ref.disableClose = !!this.loading;
    }

    public acceptMeeting() {
        this.setLoading('Accepting meeting...');
        const event = this.booking.toJSON();
        this._bookings
            .accept(this.booking.id, {
                concierge: true,
                host: this.booking.organiser.email,
                room_id: this.booking.space?.id,
                icaluid: this.booking.icaluid,
                start: event.start,
                end: event.end,
            })
            .then(() => {
                this._service.notifySuccess('Meeting approved.');
                this.booking.approval_status[this.booking.space.id] = 'accepted';
                this.setLoading(null);
                this.event.emit({ reason: 'action', metadata: 'approved' });
            }, this._on_error('Unable to approving meeting'));
    }

    public declineMeeting() {
        this.setLoading('Declining meeting...');
        this._bookings
            .decline(this.booking.id, {
                booking_id: this.booking.id,
                organiser: this.booking.organiser.email,
                icaluid: this.booking.icaluid,
                start: Math.floor(this.booking.date / 1000),
                end: Math.floor(this.booking.date / 1000) + this.booking.duration * 60,
                room_id: this.booking.space.id,
            })
            .then(() => {
                this._service.notifySuccess('Meeting declined.');
                this.booking.approval_status[this.booking.space.id] = 'declined';
                this.setLoading(null);
                this.event.emit({ reason: 'action', metadata: 'declined' });
            }, this._on_error('Unable to declining meeting'));
    }

    /** Open modal to confirm deletion of booking */
    public confirmDeleteMeeting() {
        const ref = this._dialog.open<DeleteConfirmModalComponent>(DeleteConfirmModalComponent);
        this.subscription(
            'confirm',
            ref.componentInstance.event.subscribe(async (event: DialogEvent) => {
                console.log('Event:', event);
                /* istanbul ignore else */
                if (event.reason === 'action') {
                    ref.componentInstance.loading = true;
                    const success = await this.deleteMeeting().catch(
                        this._on_error('Error deleting booking')
                    );
                    ref.componentInstance.loading = false;
                    ref.componentInstance.success = !!success;
                    setTimeout(() => ref.close(), 3000);
                }
            })
        );
    }

    /** Delete booking */
    private async deleteMeeting() {
        this.setLoading('Deleting meeting...');
        await this.booking.delete();
        this._service.notifySuccess('Successfully deleted booking');
        this.setLoading(null);
        this.event.emit({ reason: 'action', metadata: 'removed' });
        this._dialog_ref.close();
        return true;
    }

    /** Update booking requirements */
    public async updateRequirements(form) {
        this.setLoading('Updating requirements...');
        const value = form.value;
        const booking = new Booking({
            ...this.booking,
            ...value,
            room_ids: value.space_list.map((space) => space.id),
        });
        const new_booking: Booking = (await booking.save().catch((err) => {
            this._service.notifyError(`Error updating requirements. Error: ${err.message || err}`);
        })) as any;
        if (new_booking) {
            this._data.booking = new_booking;
            this._service.notifySuccess('Successfully updated requirements');
        }
        this.setLoading(null);
    }
}
