
import { MockHttpRequestHandler } from '@placeos/ts-client';

import { generateMockUser } from 'src/app/services/data/users/user.utilities';
import { API } from './common.mock';

window.control = window.control || {};
window.control.systems = window.control.systems || {};
window.control.handlers = window.control.handlers || [];

export const MOCK_USERS = Array(Math.floor(Math.random() * 300 + 100)).fill(0)
    .map(i => generateMockUser());
export const MOCK_CONTACTS = Array(Math.floor(Math.random() * 300 + 100)).fill(0)
    .map(i => generateMockUser({ external: true }));

export const PREDEFINED_USERS = [
    'Jonathan McFarlane',
    'Stephen Von Takach',
    'Alex Sorafumo',
    'Candy Russo',
    'Cristina Boston',
    'Eugene Murphy',
    'Ben Hoad',
    'Kim Burgess',
    'Desk Test',
    'Space Test',
    'Wireless Test',
    'Zo-Kalar'
];

// Add predefined user to user list
for (const user of PREDEFINED_USERS) {
    const id = user.split(' ').join('.').toLowerCase();
    const new_user = generateMockUser({ id, name: user });
    new_user.location = true;
    MOCK_USERS.push(new_user);
}

// Add handler for users index
window.control.handlers.push({
    path: `${API}/users`,
    metadata: MOCK_USERS,
    callback: (event) => {
        if (event.query_params.q) {
            const query = event.query_params.q.toLowerCase();
            return MOCK_USERS.filter(i =>
                (i.id || '').toLowerCase().indexOf(query) >= 0 ||
                (i.name || '').toLowerCase().indexOf(query) >= 0 ||
                (i.email || '').toLowerCase().indexOf(query) >= 0
            );
        }
        return MOCK_USERS;
    }
} as MockHttpRequestHandler);

// Add handler for users show
window.control.handlers.push({
    path: `${API}/users/:id`,
    metadata: MOCK_USERS,
    callback: (event) => {
        const user = MOCK_USERS.find(i => i.id === event.route_params.id || i.name === event.route_params.id || i.email === event.route_params.id);
        if (user) {
            return user;
        } else if (event.route_params.id === 'current') {
            return MOCK_USERS[MOCK_USERS.length - 1];
        }
        throw { status: 404, message: 'User not found' };
    }
} as MockHttpRequestHandler);

// Add handler for contacts index
window.control.handlers.push({
    path: `${API}/contacts`,
    metadata: MOCK_CONTACTS,
    callback: (event) => {
        if (event.query_params.q) {
            const query = event.query_params.q.toLowerCase();
            return MOCK_CONTACTS.filter(i =>
                (i.id || '').toLowerCase().indexOf(query) >= 0 ||
                (i.name || '').toLowerCase().indexOf(query) >= 0 ||
                (i.email || '').toLowerCase().indexOf(query) >= 0
            );
        }
        return MOCK_USERS;
    }
} as MockHttpRequestHandler);

// Add handler for contacts show
window.control.handlers.push({
    path: `${API}/contacts/:id`,
    metadata: MOCK_USERS,
    callback: (event) => {
        const user = MOCK_USERS.find(i => i.id === event.route_params.id || i.name === event.route_params.id);
        if (user) {
            return user;
        }
        throw { status: 404, message: 'User not found' };
    }
} as MockHttpRequestHandler);


