import { HashMap } from "../../../shared/utilities/types.utilities";

export interface CateringAvailability {
    /** Month of the year that the item starts being available */
    readonly from_month: number;
    /** Month of the year that the item ends being available */
    readonly to_month: number;
}

export class CateringItem {
    /** Unique ID of the catering item */
    public readonly id: string;
    /** Display name of the catering item */
    public readonly name: string;
    /** Description of the item */
    public readonly description: string;
    /** URL to the image associated with the item */
    public readonly image_path: string;
    /** Type of catering item */
    public readonly catering_type: string;
    /** Availability of the item */
    public readonly available: CateringAvailability;
    /** Time in hours that is needed to prepare the item beforehand */
    public readonly prior_notice: number;
    /** Cost of the item without decimals */
    public readonly unit_price: number;
    /** Cost of the item without decimals */
    public readonly supplier_cost: number;
    /** Minimum number this item allowed in an order */
    public readonly minimum_quantity: number;
    /** Maximum number this item allowed in an order */
    public readonly maximum_quantity: number;
    /** List of categories that the item is contained in */
    public readonly parent_categories: readonly string[];
    /** Allegen information associated with the item */
    public readonly allergy: string;
    /** Whether the supply of this item has run out */
    public readonly out_of_stock: boolean;
    /** Whether item can be ordered within the time limit set by the building */
    public readonly order_anytime: boolean;
    /** Number of child items that must be selected as part of the package */
    public readonly must_select: number;
    /** Number of this item in the assoicated order */
    private _amount = 0;
    /** Unique ID of the catering item */
    private instance_id: string;


    public get can_order_anytime(): boolean {
        return this.order_anytime;
    }

    /** Number of this item in the assoicated order */
    public get amount(): number {
        return this._amount || 0;
    }

    /** Total cost of the amount of items set */
    public get total(): number {
        return (this._amount * this.unit_price) || 0;
    }

    constructor(data: HashMap) {
        this.instance_id = `item-${Math.floor(Math.random() * 999_999_999)}`
        this.id = data.id || '';
        this.name = data.name || '';
        this.available = {
            from_month: (data.available ? data.available.from_month : data.available_from) || -1,
            to_month: (data.available ? data.available.to_month : data.available_to) || -1,
        };
        this.description = data.description || '';
        this.prior_notice = data.prior_notice || data.notice;
        this.unit_price = data.unit_price || data.price || 0;
        this.supplier_cost = data.supplier_cost;
        this.minimum_quantity = Math.max(0, data.minimum_quantity || 0);
        this.maximum_quantity = Math.max(1, data.maximum_quantity || 999);
        this.parent_categories = data.category_ids || data.parent_categories || data.categories;
        this.order_anytime = !!data.order_anytime;
        this.image_path = data.image_path || '';
        this.catering_type = data.catering_type;
        this.must_select = data.must_select || 0;
        this.out_of_stock = !!data.out_of_stock || data.hide;
        this._amount = data._amount || data.amount || 0;
        this.allergy = data.allergy;
    }

    public setAmount(amount: number = 0): void {
        if (amount <= this._amount) {
            if (amount < this.minimum_quantity) {
                amount = 0;
            }
        } else {
            if (amount < this.minimum_quantity) {
                amount = this.minimum_quantity;
            }
        }
        this._amount = Math.max(0, Math.min(this.maximum_quantity, amount));
    }

    /**
     * Increase amount of the item the order
     * @param amount Amount to increase by
     */
    public addToOrder(amount: number = 1) {
        amount = Math.max(1, amount);
        this._amount += amount;
        if (this._amount < 0) {
            this._amount = 0;
        } else if (this._amount < this.minimum_quantity) {
            this._amount = this.minimum_quantity;
        } else if (this._amount > this.maximum_quantity) {
            this._amount = this.maximum_quantity;
        }
    }

    /**
     * Decrease amount of the item the order
     * @param amount Amount to decrease by
     */
    public removeFromOrder(amount: number = 1) {
        amount = Math.max(1, amount);
        this._amount -= amount;
        if (this._amount < 0) {
            this._amount = 0;
        } else if (this._amount < this.minimum_quantity) {
            this._amount = 0;
        }
    }

    /**
     * Convert class object into plain object
     */
    public toJSON(this: CateringItem): HashMap {
        const obj: any = { ...this };
        // Remove local private members
        delete obj._changes;
        delete obj._server_names;
        // Convert remaining members to be public
        obj.price = obj.unit_price;
        obj.hide = obj.out_of_stock;
        obj.categories = obj.parent_categories;
        const keys = Object.keys(obj);
        for (const key of keys) {
            if (key[0] === '_') {
                const new_key = key.substr(1);
                obj[new_key] = obj[key];
                delete obj[key];
            } else if (obj[key] === undefined) {
                delete obj[key];
            }
        }
        return obj;
    }
}
