import { Component, OnInit, Inject, Output, EventEmitter } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { FormGroup, FormControl } from '@angular/forms';

import { UsersService } from 'src/app/services/data/users/users.service';
import { Booking } from 'src/app/services/data/bookings/booking.class';
import { Identity, DialogEvent } from 'src/app/shared/utilities/types.utilities';
import { humaniseDuration } from 'src/app/shared/utilities/general.utilities';
import { BaseDirective } from 'src/app/shared/base.directive';
import { ApplicationService } from 'src/app/services/app.service';
import { BookingNote } from 'src/app/services/data/bookings/booking.interfaces';

import * as dayjs from 'dayjs';

export interface PrivateDetailsModalData {
    booking: Booking;
}

@Component({
    selector: 'a-private-details-modal',
    templateUrl: './private-details-modal.component.html',
    styleUrls: ['./private-details-modal.component.scss'],
})
export class PrivateDetailsModalComponent extends BaseDirective implements OnInit {
    /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** Form Fields for private details */
    public form: FormGroup;
    /** Whether changes are being saved */
    public loading: boolean;
    /** List of available durations for setup and breakdown */
    public available_durations: Identity[] = new Array(5)
        .fill(0)
        .map((_, i) => ({ id: i * 15, name: humaniseDuration(i * 15) || '0 minutes' }));

    public get booking(): Booking {
        return this._data.booking;
    }

    constructor(
        private _service: ApplicationService,
        private _users: UsersService,
        private _dialog_ref: MatDialogRef<PrivateDetailsModalComponent>,
        @Inject(MAT_DIALOG_DATA) private _data: PrivateDetailsModalData
    ) {
        super();
    }

    public ngOnInit(): void {
        const note = this.booking.notes.find(
            (note) => note.type === 'private' && note.space === this.booking.space.id
        ) || { message: '' };
        this.form = new FormGroup({
            setup: new FormControl(this.booking.setup || 0),
            breakdown: new FormControl(this.booking.breakdown || 0),
            notes: new FormControl(note.message),
        });
    }

    public saveChanges() {
        if (this.form.dirty) {
            const old_notes = this.booking.notes.filter(
                (note) => !(note.type === 'private' && note.space === this.booking.space.id)
            );
            const notes = [
                ...old_notes,
                {
                    type: 'private',
                    date: dayjs().valueOf(),
                    message: this.form.value.notes,
                    author: this._users.current.name,
                    space: this.booking.space.id,
                } as BookingNote,
            ];
            const new_booking = new Booking({
                ...this.booking.toJSON(),
                ...this.form.value,
                notes,
            });
            this.loading = true;
            this._dialog_ref.disableClose = true;
            new_booking.save().then(
                (booking) => {
                    this._service.notifySuccess('Successfully updated private details');
                    this.event.emit({ reason: 'done', metadata: booking });
                    this._dialog_ref.close();
                },
                (err) => {
                    this._dialog_ref.disableClose = false;
                    this._service.notifyError(
                        `Unable to save changes to private details. Error: ${err.message || err}`
                    );
                    this.loading = false;
                }
            );
        } else {
            this._dialog_ref.close();
        }
    }
}
