import { Component, OnInit, Inject, Output, EventEmitter } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialog } from '@angular/material/dialog';

import { Space, RecurrenceAvailability } from 'src/app/services/data/spaces/space.class';
import { ApplicationService } from 'src/app/services/app.service';
import { SpaceSelectModalComponent, SpaceSelectModalData } from 'src/app/overlays/space-select-modal/space-select-modal.component';
import { User } from 'src/app/services/data/users/user.class';
import { BaseDirective } from 'src/app/shared/base.directive';
import { unique } from 'src/app/shared/utilities/general.utilities';
import { BookingRecurrenceSpaceDetails } from 'src/app/services/data/bookings/booking.interfaces';
import { DialogEvent } from 'src/app/shared/utilities/types.utilities';

import * as dayjs from 'dayjs';
import { SpacesService } from 'src/app/services/data/spaces/spaces.service';

export interface RecurrenceSpacesModalData {
    /** Space that has recurrence unavailability */
    space: Space;
    /** Duration of the booking */
    duration: number;
    /** Duration of the booking */
    host: User;
    /** List of current conflict matchups */
    conflicts: BookingRecurrenceSpaceDetails[];
}

export interface OccurrenceDetails extends RecurrenceAvailability {
    /** Display string for the occurence datetime */
    date_string: string;
    /** Name of the space that replaces the space on this occurrence */
    replaced_by: string;
}

@Component({
    selector: 'a-recurrence-spaces-modal',
    templateUrl: './recurrence-spaces-modal.component.html',
    styleUrls: ['./recurrence-spaces-modal.component.scss']
})
export class RecurrenceSpacesModalComponent extends BaseDirective implements OnInit {
    /** Emitter for modal events */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** List of occurence details for space booking */
    public occurrences: OccurrenceDetails[] = [];
    /** List of space details for occurrences */
    public space_details: BookingRecurrenceSpaceDetails[] = []

    /** Space to setup recurrence for */
    public get space(): Space {
        return this._data.space;
    }

    /** Whether all occurences have a valid space */
    public get occurrences_valid(): boolean {
        return this.occurrences.reduce((valid, event) => valid && (event.available || !!event.replaced_by), true);
    }

    constructor(
        @Inject(MAT_DIALOG_DATA) private _data: RecurrenceSpacesModalData,
        private _dialog: MatDialog,
        private _spaces: SpacesService
    ) {
        super();
    }

    ngOnInit(): void {
        this.space_details = [].concat(this._data.conflicts || []);
        this.updateOccurrences();
    }

    public save() {
        this.event.emit({ reason: 'done', metadata: this.space_details });
    }

    public selectSpace(occurrence: OccurrenceDetails) {
        const ref = this._dialog.open<SpaceSelectModalComponent, SpaceSelectModalData>(
            SpaceSelectModalComponent,
            {
                width: 'auto',
                height: 'auto',
                maxWidth: '95vw',
                data: {
                    spaces: [],
                    zone_ids: this.space.level.building_id,
                    date: occurrence.date * 1000,
                    duration: this._data.duration,
                    host: this._data.host,
                    multiple: false
                }
            }
        );
        this.subscription(
            'new_spaces',
            ref.componentInstance.event.subscribe(event => {
                /* istanbul ignore else */
                if (event.reason === 'done') {
                    const space = event.metadata[0];
                    /* istanbul ignore else */
                    if (space) {
                        this.space_details.unshift({
                            date: occurrence.date,
                            space: space.email,
                            replaces: this.space.email
                        });
                        this.space_details = unique(this.space_details, 'date');
                    }
                    this.updateOccurrences();
                    ref.close();
                    this.unsub('new_spaces');
                }
            })
        );
    }

    private updateOccurrences() {
        this.occurrences = this._data.space.recurr_available.map(event => {
            const space_details = this.space_details.find(details => details.date === event.date && details.replaces === this.space.email);
            const email = (space_details || {}).space;
            const space = this._spaces.find(email) || { email, name: email };
            return {
                ...event,
                date_string: dayjs(event.date * 1000).format('dddd, DD MMMM YYYY'),
                replaced_by: space.name
            }
        });
    }
}
