import { MockHttpRequestHandler } from '@placeos/ts-client';

import { MOCK_USERS } from "./users.mock";
import { MOCK_SPACES } from './spaces.mock';
import { MOCK_BUILDINGS } from './buildings.mock';
import { randomInt } from '../../utilities/general.utilities';
import { generateMockLocation } from 'src/app/services/data/location/location.utilities';

export const MOCK_LOCATIONS: any[] = [];

const spaces = MOCK_SPACES.map(space => space.map_id);
const levels = MOCK_BUILDINGS.reduce((lvls, bld) => {
    bld.settings.discovery_info.levels.forEach(lvl => lvls.push(lvl.level_id));
    return lvls;
}, []);

for (const user of MOCK_USERS) {
    if (user.location || randomInt(99999) % 2 === 0) {
        user.location = generateMockLocation(null, spaces, levels);
        MOCK_LOCATIONS.push(user.location);
    }
}

setTimeout(() => {
    window.control.handlers.push({
        path: '/api/staff/people/:email',
        metadata: MOCK_LOCATIONS,
        callback: (event) => {
            if (event.route_params.email) {
                const user = MOCK_USERS.find(user => user.email === event.route_params.email || user.id === event.route_params.email);
                if (user) {
                    return user.location;
                }
            }
            throw { status: 404, message: `Unable to locate user with email ${event.route_params.email}` };
        }
    } as MockHttpRequestHandler);
});


