import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';

import { Booking } from 'src/app/services/data/bookings/booking.class';
import {
    VisitorsFormModalComponent,
    VisitorsFormModalData,
} from './overlays/visitors-form-modal/visitors-form-modal.component';

import * as dayjs from 'dayjs';
import { SpacesService } from 'src/app/services/data/spaces/spaces.service';
import { OrganisationService } from 'src/app/services/data/organisation/organisation.service';

@Component({
    selector: 'app-visitors',
    templateUrl: './visitors.component.html',
    styleUrls: ['./visitors.component.scss'],
})
export class VisitorsComponent implements OnInit {
    /** Currently selected date */
    public date: string;
    /** Search filter string */
    public search_str: string;
    /** Whether all bookings should be listed */
    public show_all: boolean;

    public get has_visitor_space(): boolean {
        const building = this._org.building;
        return building && !!this._spaces.find(building.visitor_space || building.holding_bay);
    }

    constructor(
        private _dialog: MatDialog,
        private _spaces: SpacesService,
        private _org: OrganisationService
    ) {}

    public ngOnInit(): void {}

    public newVisitorMeeting() {
        let date = dayjs();
        date = date.minute(Math.ceil(date.minute() / 5) * 5);
        const building = this._org.building;
        const space = this._spaces.find(building.visitor_space || building.holding_bay);
        const booking = new Booking({ date: date.valueOf(), duration: -1, room_id: [space.email] });
        this._dialog.open<VisitorsFormModalComponent, VisitorsFormModalData>(
            VisitorsFormModalComponent,
            {
                data: {
                    booking,
                },
            }
        );
    }
}
