import { Component, Input, SimpleChanges, OnChanges } from '@angular/core';

import { Space } from 'src/app/services/data/spaces/space.class';

import * as dayjs from 'dayjs';
import { Booking } from 'src/app/services/data/bookings/booking.class';

@Component({
    selector: 'week-view-day-display',
    templateUrl: './day-display.component.html',
    styleUrls: ['./day-display.component.scss']
})
export class WeekViewDayDisplayComponent implements OnChanges {
    /** Date to display bookings for */
    @Input() public date: number;
    /** List of spaces to display bookings for */
    @Input() public spaces: Space[];
    /** List of bookings for this date */
    public booking_list: Booking[] = [];

    /** Display string for the set date */
    public get date_display(): string {
        const date = dayjs(this.date);
        return date.format('ddd, DD MMM YYYY');
    }

    /** Whether the date set is today */
    public get is_today(): boolean {
        return dayjs(this.date).isSame(dayjs(), 'd');
    }

    public ngOnChanges(changes: SimpleChanges) {
        /* istanbul ignore else */
        if (changes.date || changes.spaces) {
            this.updateBookings();
        }
    }

    private updateBookings(): void {
        let bookings = [];
        this.spaces.forEach(space => {
            bookings = bookings.concat(space.bookingsFor(this.date));
        });
        bookings.sort((a, b) => a.date - b.date);
        this.booking_list = bookings;
    }
}
