import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormControl } from '@angular/forms';

import { CateringCategory } from 'src/app/services/data/catering/catering-category.class';

@Component({
    selector: 'a-category-display',
    templateUrl: './category.component.html',
    styleUrls: ['./category.component.scss']
})
export class CateringMenuCategoryComponent implements OnInit {
    /** Category to display details for */
    @Input() public category: CateringCategory;
    /** Form control for the list of order items */
    @Input() public items: FormControl;
    /** Currency symbol for the active space */
    @Input() public symbol: string;
    /** Whether item should be disabled if not available anytime */
    @Input() public disabled: boolean;
    /** Emitter for close events */
    @Output() public close = new EventEmitter<void>();

    constructor() {}

    ngOnInit(): void {}
}
