import { Component, Input } from '@angular/core';

import { Booking } from 'src/app/services/data/bookings/booking.class';
import { CateringOrder } from 'src/app/services/data/catering/catering-order.class';
import { CateringCategory } from 'src/app/services/data/catering/catering-category.class';
import { ApplicationService } from 'src/app/services/app.service';
import { BookingsService } from 'src/app/services/data/bookings/bookings.service';

@Component({
    selector: 'order-details-items',
    templateUrl: './items.component.html',
    styleUrls: ['./items.component.scss'],
})
export class OrderDetailsItemsComponent {
    /** Booking details to display */
    @Input() public booking: Booking;
    /** Catering order to display notes for */
    @Input() public order: CateringOrder;
    /** Whether order is being edited */
    public editing: boolean;
    /** Whether order is being updated */
    public loading: boolean;

    /** List of items in the order */
    public get items(): CateringCategory[] {
        return (this.order.items as any) || [];
    }

    constructor(private _service: ApplicationService, private _bookings: BookingsService) {}

    public editOrder() {
        this.editing = true;
    }

    /** Handle changes to catering order */
    public async handleOrderEvent(order: CateringOrder) {
        /* istanbul ignore else */
        if (order) {
            const orders = [...this.booking.catering];
            const index = orders.findIndex((an_order) => order.id === an_order.id);
            if (index < 0) {
                orders.push(order);
            } else {
                orders.splice(index, 1, order);
            }
            this.loading = true;
            const new_booking = new Booking({ ...this.booking, catering: orders });
            const booking = await this._bookings
                .save(new_booking)
                .catch((err) =>
                    this._service.notifyError(
                        `Error unable to update order. Error: ${err.message || err}`
                    )
                );
            if (booking) {
                this.booking = booking;
                this.order = order;
                this.editing = false;
            }
            this.loading = false;
        }
    }
}
