import { Component, OnInit, Inject, Output, EventEmitter } from '@angular/core';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Observable, Subject, of } from 'rxjs';
import { debounceTime, distinctUntilChanged, switchMap, catchError, map } from 'rxjs/operators';

import { ApplicationService } from 'src/app/services/app.service';
import { BaseDirective } from 'src/app/shared/base.directive';
import { User } from 'src/app/services/data/users/user.class';
import { Space, SpaceBookingRuleOptions } from 'src/app/services/data/spaces/space.class';
import { Building } from 'src/app/services/data/organisation/building.class';
import { Identity, HashMap, DialogEvent } from 'src/app/shared/utilities/types.utilities';
import { unique } from 'src/app/shared/utilities/general.utilities';
import { SpacesService } from 'src/app/services/data/spaces/spaces.service';
import { OrganisationService } from 'src/app/services/data/organisation/organisation.service';

export interface SpaceSelectModalData {
    /** List of selected spaces */
    spaces: Space[];
    /** Start time for availability of spaces */
    date: number;
    /** Duration of the availability block to look for */
    duration: number;
    /** Host of the booking being made */
    host: User;
    /** Whether multiple spaces can be selected */
    multiple: boolean;
    /** Force zone ids */
    zone_ids?: string;
}

@Component({
    selector: 'a-space-select-modal',
    templateUrl: './space-select-modal.component.html',
    styleUrls: ['./space-select-modal.component.scss']
})
export class SpaceSelectModalComponent extends BaseDirective implements OnInit {
    /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** Whether space list is being loaded */
    public loading: boolean;
    /** List of available spaces to select from */
    public spaces: Space[] = [];
    /** Active building to filter spaces on */
    public active_building: Building;
    /** Active building to filter spaces on */
    public active_type: Identity;
    /** List of items from an API search */
    public search_results$: Observable<Space[]>;
    /** Subject holding the value of the search */
    public readonly search$ = new Subject<string>();
    /** List of currently selected spaces */
    public selected_spaces: Space[] = [];
    /** Mapping as to whether a space is by request */
    public request_map: HashMap<boolean> = {};
    /** Whether selected list should be shown on mobile */
    public show_list: boolean;

    /** List of available buildings */
    public types: Identity[] = []

    /** List of available buildings */
    public get buildings(): Building[] {
        return this._org.buildings;
    }

    /** Whether multiple spaces can be selected */
    public get multiple(): boolean {
        return !!this._data.multiple;
    }

    public get has_zone(): boolean {
        return !!this._data.zone_ids;
    }

    constructor(
        private _spaces: SpacesService,
        private _org: OrganisationService,
        @Inject(MAT_DIALOG_DATA) private _data: SpaceSelectModalData
    ) {
        super();
    }

    public ngOnInit(): void {
        this.selected_spaces = [].concat(this._data.spaces || []);
        // Listen for input changes
        this.search_results$ = this.search$.pipe(
            debounceTime(400),
            distinctUntilChanged(),
            switchMap(_ => {
                this.loading = true;
                const zone_ids: string[] = [];
                /* istanbul ignore else */
                if (this.active_building) {
                    zone_ids.push(this.active_building.id);
                }
                /* istanbul ignore else */
                if (this.active_type) {
                    zone_ids.push(`${this.active_type.id}`);
                }
                return this._spaces.available({
                    zone_ids: this._data.zone_ids || zone_ids.join(','),
                    date: this._data.date,
                    duration: this._data.duration
                });
            }),
            catchError(_ => of([])),
            map((list: Space[]) => {
                this.loading = false;
                const selected = this.selected_spaces;
                return list.filter((item: any) => !selected.find(space => space.id === item.id));
            })
        );
        // Process API results
        this.subscription(
            'search_results',
            this.search_results$.subscribe(list => {
                this.spaces = list;
                this.update();
            })
        );
        this.active_building = this._org.building;
        this.types = [{ id: '', name: 'Any Space Type' } as any].concat(
            this._org.space_types || []
        );
        this.active_type = this.types[0];
        this.search$.next('');
    }

    /** Add space to selected list */
    public addSpace(space: Space) {
        this.selected_spaces.push(space);
        this.selected_spaces = unique(this.selected_spaces, 'id');
    }

    /** Remove space from selected list */
    public removeSpace(space: Space) {
        this.selected_spaces = this.selected_spaces.filter(a_space => a_space.id !== space.id);
    }

    /** Emit current state of the selected list */
    public save(list: Space[]) {
        this.event.emit({ reason: 'done', metadata: list });
    }

    private update() {
        const map = {};
        const options: SpaceBookingRuleOptions = {
            date: this._data.date,
            duration: this._data.duration,
            host: this._data.host
        };
        this.selected_spaces.forEach(space => (map[space.id] = space.byRequest(options)));
        this.spaces.forEach(space => (map[space.id] = space.byRequest(options)));
        this.request_map = map;
    }
}
