import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormControl } from '@angular/forms';

import { ApplicationService } from 'src/app/services/app.service';
import { BaseDirective } from 'src/app/shared/base.directive';
import { Building } from 'src/app/services/data/organisation/building.class';
import { unique } from 'src/app/shared/utilities/general.utilities';

import * as dayjs from 'dayjs';
import { ANIMATION_SHOW_CONTRACT_EXPAND } from 'src/app/shared/globals/angular-animations';
import { OrganisationService } from 'src/app/services/data/organisation/organisation.service';

@Component({
    selector: 'booking-space-filters',
    templateUrl: './filters.component.html',
    styleUrls: ['./filters.component.scss'],
    animations: [ANIMATION_SHOW_CONTRACT_EXPAND]
})
export class BookingSpaceFiltersComponent extends BaseDirective implements OnInit {
    /** Date selected for the booking */
    @Input() public date: FormControl;
    /** List of location IDs being filtered on */
    @Input() public locations: string[] = [];
    /** Emitter for changes to location filter */
    @Output() public locationsChange = new EventEmitter<string[]>();
    /** List of available buildings */
    public building_list: Building[] = [];
    /** Whether to show the location list */
    public show_locations: boolean;

    /** Whether active date is current day */
    public get is_today(): boolean {
        const date = dayjs(this.date.value);
        return dayjs().isSame(date, 'd');
    }

    /** Display string for the selected date */
    public get date_display(): string {
        const date = dayjs(this.date.value);
        return this.is_today ? 'Today' : date.format('DD MMM YYYY');
    }

    /** List of filtered location */
    public get location_list(): Building[] {
        return this.locations.map(id => this.building_list.find(bld => bld.id === id));
    }

    constructor(private _org: OrganisationService) {
        super();
    }

    public changeDate(amount: number) {
        let date = dayjs(this.date.value).add(amount, 'd');
        const now = dayjs().startOf('m');
        if (date.isBefore(now, 's')) {
            date = now;
        }
        this.date.setValue(date.valueOf());
    }

    public ngOnInit(): void {
        this.subscription('buildings', this._org.listen('buildings').subscribe((bld_list) => {
            this.building_list = bld_list;
        }));
    }

    /**
     * Update location filter to include the given building
     * @param building Building to add
     */
    public addLocationFilter(building: Building): void {
        this.locations.push(building.id);
        this.locations = unique(this.locations);
        this.locationsChange.emit(this.locations);
    }

    /**
     * Remove location from the active filters
     * @param building Building to remove
     */
    public removeLocationFilter(building: Building) {
        this.locations.splice(
            this.locations.findIndex(id => building.id === id),
            1
        );
        this.locationsChange.emit(this.locations);
    }
}
