const MINUTE = 1;
const HOUR = 60;
const DAY = 24 * HOUR;
const WEEK = 7 * DAY;
const MONTH = 30 * DAY;

const DURATION_MAP: { [duration: string]: number } = {
    month: MONTH,
    months: MONTH,
    week: WEEK,
    weeks: WEEK,
    day: DAY,
    days: DAY,
    hour: HOUR,
    hours: HOUR,
    minute: MINUTE,
    minutes: MINUTE,
};

let BOOKING_COUNT = 0;
let BOOKING_DATE = dayjs().hour(6).minute(0).subtract(10, 'd').startOf('m');

/**
 * Set the initial time used for generating mock bookings
 * @param time New initial time as ms from UTC epoch
 */
export function setMockBookingStartDatetime(time: number) {
    BOOKING_DATE = dayjs(time).startOf('m');
}

/**
 * Create mock raw API data for a booking
 * @param override Overrides the properties of the generated booking with it's own
 */
export function generateMockBooking(override: HashMap = {}) {
    const id = `booking-${BOOKING_COUNT++}`;
    BOOKING_DATE = BOOKING_DATE.add(Math.floor(Math.random() * 4 + 2) * 15, 'm');
    const start = BOOKING_DATE.valueOf();
    const duration = Math.floor(Math.random() * 4 + 2) * 15;
    BOOKING_DATE = BOOKING_DATE.add(Math.floor(Math.random() * 4) * 15, 'm');
    return {
        id,
        icaluid: padZero(Math.floor(Math.random() * 99999999), 8),
        title: `${faker.commerce.productName()} Meeting`,
        attendees: Array(Math.floor(Math.random() * 5 + 2))
            .fill(0)
            .map((i) => generateMockUser(override.users)),
        organiser: generateMockUser(),
        start_epoch: dayjs(start).unix(),
        end_epoch: dayjs(start).add(duration, 'm').unix(),
        description: faker.lorem.paragraph(),
        notes: [{ type: 'other', message: faker.lorem.paragraph() }],
        location: faker.address.city(),
        has_catering: Math.floor(Math.random() * 34567) % 3 === 0,
        booking_type: ['internal', 'training', 'setup', 'client', 'Interview'][randomInt(5)],
        setup: { 'space-01': Math.max(0, (randomInt(12) - 6) * 5) },
        breakdown: { 'space-01': Math.max(0, (randomInt(12) - 6) * 5) },
        status: {},
        catering: [],
        room_ids: [],
        ...override,
    };
}

export interface IBookingSlot {
    start: number;
    end: number;
}

/**
 * Generate a list of free time slots between the given bookings
 * @param list List of bookings to find slots between
 * @param min_size Minimum length of a free slot in minutes
 */
export function getFreeBookingSlots(list: Booking[], min_size: number = 30): IBookingSlot[] {
    /* istanbul ignore else */
    if (!list) {
        return [
            {
                start: 0,
                end: dayjs().startOf('m').valueOf() * 10,
            },
        ];
    }
    const slots: IBookingSlot[] = [];
    let start = dayjs(0);
    list.sort((a, b) => a.date - b.date);
    for (const booking of list) {
        const bkn_start = dayjs(booking.date);
        const bkn_end = bkn_start.add(booking.duration, 'm');
        if (bkn_start.isAfter(start)) {
            const diff = Math.abs(bkn_start.diff(start, 'm'));
            /* istanbul ignore else */
            if (diff >= min_size) {
                slots.push({ start: start.valueOf(), end: bkn_start.valueOf() });
            }
            start = bkn_end;
        } else if (start.startOf('m').valueOf() === bkn_start.startOf('m').valueOf()) {
            start = bkn_end;
        }
    }
    slots.push({
        start: start.valueOf(),
        end: dayjs().startOf('m').valueOf() * 10,
    });
    return slots;
}

/**
 * Get the next free time slot from the given bookings
 * @param list List of bookings to find the next slot
 * @param date Date to find next slot after in ms since UTC epoch
 * @param min_size Minimum length of the free slot in minutes
 */
export function getNextFreeBookingSlot(
    list: Booking[],
    date: number = dayjs().valueOf(),
    min_size: number = 30
): IBookingSlot {
    const slots = getFreeBookingSlots(list, min_size);
    const time = dayjs(date).startOf('m').second(1);
    for (const block of slots) {
        const start = dayjs(block.start).startOf('m');
        const end = dayjs(block.end).startOf('m');
        if (start.isAfter(time, 's')) {
            return block;
        } else if (time.isBefore(end, 's')) {
            const duration = end.diff(time, 'm');
            /* istanbul ignore else */
            if (duration >= min_size) {
                return block;
            }
        }
    }
    return slots[slots.length - 1];
}

/**
 * Generate form fields for the given booking
 * @param booking Booking to generate form for
 * @param current_user Current user of the system to default as the host
 */
export function generateBookingForm(
    booking: Booking,
    use_fields: AvailableBookingFields[]
): FormGroup {
    if (!booking) {
        throw Error('No booking passed');
    }
    const user_service = ServiceManager.serviceFor(User);
    const current_user =
        user_service.current ||
        new User({ id: 'local-user', name: 'Local User', email: 'local@place.tech' });
    const fields: HashMap<FormControl> = {
        id: new FormControl(booking.id || ''),
        space_list: new FormControl(booking.space_list, []),
        date: new FormControl(booking.date, [Validators.required]),
        duration: new FormControl(booking.duration),
        organiser: new FormControl(booking.organiser || current_user, [Validators.required]),
        organiser_email: new FormControl(booking.organiser.email || current_user.email, [
            Validators.required,
        ]),
        attendees: new FormControl(booking.attendees, []),
        title: new FormControl(booking.title || '', [Validators.required]),
        type: new FormControl(booking.type),
        body: new FormControl(booking.body),
        notes: new FormControl(booking.notes),
        equipment_codes: new FormControl(booking.equipment_codes),
        expected_attendees: new FormControl(booking.expected_attendees),
        catering: new FormControl(
            booking.catering.map(
                (order) => new CateringOrder({ ...order, booking_date: booking.date })
            )
        ),
        recurrence: new FormControl(booking.recurrence),
        all_day: new FormControl(!!booking.all_day),
        has_catering: new FormControl(!!booking.has_catering),
        needs_space: new FormControl(true),
    };
    fields.duration.setValidators([Validators.required, validateEndTime(fields.date)]);
    /* istanbul ignore else */
    if (booking.id) {
        fields.organiser.disable();
        /* istanbul ignore else */
        if (dayjs(booking.date).add(booking.duration, 'm').isBefore(dayjs(), 'm')) {
            fields.date.disable();
        }
    } else {
        fields.date.setValidators([Validators.required, isFuture]);
    }
    const simplified_fields: HashMap<FormControl> = [
        'id',
        'space_list',
        'space_ids',
        'notes',
        'equipment_codes',
        'expected_attendees',
        'organiser_email',
        ...use_fields,
    ].reduce((map, key) => {
        /* istanbul ignore else */
        if (fields[key]) {
            map[key] = fields[key];
        }
        return map;
    }, {});
    if (simplified_fields.all_day) {
        simplified_fields.all_day.valueChanges.subscribe((value) => {
            if (value) {
                simplified_fields.time.disable();
                simplified_fields.duration.disable();
            } else {
                simplified_fields.time.enable();
                simplified_fields.duration.enable();
            }
        });
    }
    // Generate form group for booking item
    const form = new FormGroup(simplified_fields);
    return form;
}

/**
 * Validate whether date is in the future
 * @param control Control to check value
 */
export function isFuture(control: AbstractControl) {
    let now = dayjs().startOf('m');
    now = now.minute(Math.floor(now.minute() / 5) * 5);
    const date = dayjs(control.value);
    return date.isBefore(now, 's') ? { date: 'Date needs to be in the future' } : null;
}

/**
 * Get booking rules for the given user and space
 * @param user User to determine applicable rules
 * @param space Space to get rules for
 * @param time Booking start time in ms since UTC epoch
 * @param rules List of booking rules for the building
 */
export function rulesForSpace(options: SpaceRuleOptions): SpaceRules {
    if (!options) {
        throw Error('Options are needed to check for rule matches');
    }
    const space_rules_for_user: SpaceRules = {
        auto_approve: true,
        hide: true,
    };
    /* istanbul ignore else */
    if (options.space) {
        for (const type in options.rules) {
            if (
                options.rules.hasOwnProperty(type) &&
                options.rules[type] instanceof Array &&
                options.space.zones.find((zone) => zone === type)
            ) {
                for (const rule_block of options.rules[type]) {
                    /* istanbul ignore else */
                    if (
                        checkRules({
                            user: options.user,
                            space: options.space,
                            time: options.time,
                            recurr_end: options.recurr_end,
                            duration: options.duration,
                            rules: rule_block.conditions,
                        })
                    ) {
                        const ruleset = rule_block.rules;
                        const conditions = rule_block.conditions;
                        space_rules_for_user.hide = false;
                        /* istanbul ignore else */
                        if (conditions.max_length) {
                            space_rules_for_user.max_length = stringToMinutes(
                                conditions.max_length as string
                            );
                        }
                        /* istanbul ignore else */
                        if (conditions.min_length) {
                            space_rules_for_user.min_length = stringToMinutes(
                                conditions.min_length as string
                            );
                        }
                        // NOTE: use max_length in conditions instead of book_length in rules
                        // if (ruleset.book_length) {
                        //     space_rules_for_user.max_length = stringToMinutes(ruleset.book_length as string);
                        // }
                        /* istanbul ignore else */
                        if (ruleset.auto_approve !== undefined) {
                            space_rules_for_user.auto_approve = ruleset.auto_approve;
                        }
                        break;
                    }
                }
            }
            /* istanbul ignore else */
            if (!space_rules_for_user.hide) {
                break;
            }
        }
    }
    return space_rules_for_user;
}

/**
 * Check if user matches the given ruleset
 * @param user User to determine applicable rules
 * @param space Space to get rules for
 * @param time Booking start time in ms since UTC epoch
 * @param rules List of booking rules for the building
 */
function checkRules(options: SpaceCheckOptions): boolean {
    /* istanbul ignore else */
    if (options.rules) {
        const time = dayjs(options.time);
        const recurr = options.recurr_end ? dayjs(options.recurr_end) : dayjs();
        const count = Object.keys(options.rules).length;
        let matches = 0;
        for (const key of Object.keys(options.rules)) {
            let counter = 0;
            const condition: string[] =
                options.rules[key] instanceof Array
                    ? (options.rules[key] as [])
                    : [options.rules[key] as string];
            switch (key) {
                case 'groups':
                    /* istanbul ignore else */
                    if (options.user && options.user.groups) {
                        counter = 0;
                        condition.forEach((i) =>
                            options.user.groups.find((j) => j === i) ? counter++ : null
                        );
                        /* istanbul ignore else */
                        if (counter > 0) {
                            matches++;
                        }
                    }
                    break;
                case 'locations':
                    /* istanbul ignore else */
                    if (options.user && options.user.location) {
                        counter = 0;
                        condition.forEach((i) =>
                            (options.user.last_location.name || '').indexOf(i) >= 0
                                ? counter++
                                : null
                        );
                        /* istanbul ignore else */
                        if (counter >= options.rules[key].length) {
                            matches++;
                        }
                    }
                    break;
                case 'is_before':
                    /* istanbul ignore else */
                    if (options.time) {
                        const duration = stringToMinutes(condition[0]);
                        const check = dayjs().add(duration, 'm');
                        let match = time.isBefore(check, 'm');
                        /* istanbul ignore else */
                        if (recurr) {
                            match = match && recurr.isBefore(check, 'm');
                        }
                        matches += match ? 1 : 0;
                    }
                    break;
                case 'is_after':
                    /* istanbul ignore else */
                    if (options.time) {
                        const duration = stringToMinutes(condition[0]);
                        const check = dayjs().add(duration, 'm');
                        time.isAfter(check, 'm') ? matches++ : '';
                    }
                    break;
                case 'min_length':
                    /* istanbul ignore else */
                    if (options.duration) {
                        durationGreaterThanOrEqual(options.duration, condition[0]) ? matches++ : '';
                    }
                    break;
                case 'max_length':
                    /* istanbul ignore else */
                    if (options.duration) {
                        durationGreaterThanOrEqual(condition[0], options.duration) ? matches++ : '';
                    }
                    break;
            }
        }
        return matches >= count;
    }
    return false;
}

/**
 * Get minimum duration from ruleset in minutes
 * Default to 5min
 */
export function getMinLength(rule_list: HashMap<BookingRule[]>): number {
    return Object.values(rule_list).reduce((min, block) => {
        const min_block = block.reduce((min_length, el) => {
            if (el.conditions.min_length && stringToMinutes(el.conditions.min_length) > min) {
                return stringToMinutes(el.conditions.min_length);
            } else {
                return min_length;
            }
        }, 5);
        if (min_block > min) {
            return min_block;
        } else {
            return min;
        }
    }, 5) as number;
}

/**
 * Whether the first input is greater than the last. Converts duration strings into minutes
 * @param duration_1 First input can be a number in minutes or a duration string e.g. `1 hour`
 * @param duration_2 Second input can be a number in minutes or a duration string e.g. `30 minutes`
 */
export function durationGreaterThanOrEqual(
    duration_1: string | number,
    duration_2: string | number
) {
    const first: number = typeof duration_1 === 'string' ? stringToMinutes(duration_1) : duration_1;
    const second: number =
        typeof duration_2 === 'string' ? stringToMinutes(duration_2) : duration_2;
    return first >= second;
}

/**
 * Conver time string into minutes
 * @param str timestring e.g. `'1 day'`, `'15 minutes'`, `'2 weeks'`
 */
export function stringToMinutes(str: string): number {
    const parts = str.split(' ');
    return +parts[0] * DURATION_MAP[parts[1]];
}

/**
 * Get current status within bookings
 * @param bookings List of bookings
 * @param host Host of the new event
 * @param date Datetime of the new event
 */
export function statusFromBookings(
    bookings: Booking[],
    bookable: boolean,
    requestable: boolean,
    date: number = dayjs().valueOf()
) {
    const free_slots = getFreeBookingSlots(bookings);
    const now = dayjs(date);
    const next_free_slot = free_slots.find((slot) => {
        const start = dayjs(slot.start);
        const end = dayjs(slot.end);
        return start.isAfter(now) || (now.isAfter(start, 's') && now.isBefore(end, 'm'));
    });
    const start = dayjs(next_free_slot.start);
    const end = dayjs(next_free_slot.end);
    const currently_free = now.isAfter(start, 's') && now.isBefore(end, 'm');
    const time_until_next_block = humaniseDuration(
        currently_free ? end.diff(now, 'm') : start.diff(now, 'm'),
        true
    );
    const free_tomorrow = !currently_free && !start.isSame(now, 'd');
    const free_today = currently_free && !end.isSame(now, 'd');
    return {
        status: !bookable
            ? 'Not Bookable'
            : currently_free
            ? requestable
                ? 'Available by Request'
                : 'Available'
            : 'Meeting in Progress',
        available_until: free_today
            ? 'No meetings today'
            : currently_free
            ? `Free until ${end.format(timeFormatString())}(${time_until_next_block})`
            : free_tomorrow
            ? 'Unavailable today'
            : `Free at ${start.format(timeFormatString())}(${time_until_next_block})`,
    };
}
